// @flow
// ID generator
import nanoId from 'nanoid';

// Other model functions
import { getProgramme } from '../Programme';
import { updateThreadById } from '../Thread';

// Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Beneficiary, BeneficiaryData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of beneficiaries from the programme instance
 * @param {Model} model model instance
 * @return {Array<Beneficiary>} array of beneficiaries
 **/
export function getBeneficiaries(model: Model): Array<Beneficiary> {
  // Get programme instance
  const programme = getProgramme(model);

  // Throw an error if beneficiaries is not present or invalid
  if (!programme.beneficiaries) {
    throw new InvalidModelError('Programme did not contain an array of beneficiaries');
  } else if (!Array.isArray(programme.beneficiaries)) {
    throw new InvalidModelError("Programme 'beneficiaries' was not an array");
  }

  // Return array
  return programme.beneficiaries;
}

/**
 * Returns a specific beneficiary from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id beneficiary id
 * @return {Beneficiary} beneficiary with provided id
 **/
export function getBeneficiaryById(model: Model, id: string): Beneficiary {
  // Retrieve all beneficiaries
  const beneficiaries = getBeneficiaries(model);

  // Try and find matching beneficiary
  const found = beneficiaries.find(beneficiary => beneficiary.id === id);

  // If beneficiary is not found, throw an error
  if (!found) {
    throw new InconsistentArgumentError(`Could not find beneficiary in programme with id ${id}`);
  }

  // Otherwise return found beneficiary
  return found;
}

function checkBeneficiaryData(beneficiary: BeneficiaryData): boolean {
  if (beneficiary.hasOwnProperty('name')) {
    if (typeof beneficiary.name !== 'string' || beneficiary.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided beneficiary update value name was not a string (or it was empty)'
      );
    }
  }

  if (beneficiary.hasOwnProperty('owner')) {
    if (typeof beneficiary.owner !== 'string') {
      throw new InvalidUpdateError('Provided beneficiary update value owner was not a string');
    }
  }

  if (beneficiary.hasOwnProperty('definition')) {
    if (typeof beneficiary.definition !== 'string') {
      throw new InvalidUpdateError('Provided beneficiary update value definition was not a string');
    }
  }

  return true;
}

/**
 * Returns a new model instance with a new beneficiary (with data if provided)
 * @param {Model} model model instance
 * @param {BeneficiaryData} beneficiary optional beneficiary data to store as beneficiary
 * @return {Model} updated model instance
 */
export function newBeneficiary(model: Model, beneficiary?: BeneficiaryData, id: ?string): Model {
  // Default new beneficiary
  let beneficiaryData = {
    id: id || nanoId(),
    name: `New Beneficiary ${++newCount}`,
    owner: '',
    definition: '',
  };

  // If beneficiary data was supplied
  if (beneficiary) {
    // Check validity of beneficiary data
    checkBeneficiaryData(beneficiary);

    // Override default beneficiary data
    beneficiaryData = { ...beneficiaryData, ...beneficiary };
  }

  // Retrieve programme
  const programme = getProgramme(model);

  // Define new beneficiaries array
  const newBeneficiaries = [...programme.beneficiaries, beneficiaryData];

  return {
    ...model,
    programmes: [
      {
        ...programme,
        beneficiaries: newBeneficiaries,
      },
    ],
  };
}

/**
 * Removes a beneficiary with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id beneficiary id
 * @returns {Model} updated model instance
 */
export function removeBeneficiaryById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'null'
): Model {
  // Retrieve beneficiary to check it exists
  getBeneficiaryById(model, id);

  // Define a new model to store updated cause and effect
  let modifiedModel;

  // Retrieve cause and effect from programme
  const { causeAndEffect } = getProgramme(model);

  // Update cause and effects
  switch (onDelete) {
    // Update threads with null value
    case 'null':
      modifiedModel = causeAndEffect
        .filter(thread => thread.beneficiaryId === id)
        .reduce(
          (acc, cur) =>
            updateThreadById(acc, cur.id, {
              beneficiaryId: 'none',
            }),
          model
        );
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when removing beneficiary');
  }

  // Return modified model with beneficiary removed
  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        beneficiaries: modifiedModel.programmes[0].beneficiaries.filter(b => b.id !== id),
      },
    ],
  };
}

/**
 * Sets a beneficiary's attributes by id
 * @param {Model} model model instance
 * @param {string} id beneficiary id
 * @param {BeneficiaryData} beneficiary new beneficiary
 * @return {Model} updated model instance
 */
export function updateBeneficiaryById(
  model: Model,
  id: string,
  beneficiary: BeneficiaryData
): Model {
  // Retrieve existing beneficiary to check it exists
  getBeneficiaryById(model, id);

  // Check validity of data
  checkBeneficiaryData(beneficiary);

  // Updated beneficiary array
  const updatedBeneficiaries = model.programmes[0].beneficiaries.map(b => {
    if (b.id === id) {
      return {
        ...b,
        name: beneficiary.hasOwnProperty('name') ? beneficiary.name : b.name,
        owner: beneficiary.hasOwnProperty('owner') ? beneficiary.owner : b.owner,
        definition: beneficiary.hasOwnProperty('definition')
          ? beneficiary.definition
          : b.definition,
      };
    }
    return b;
  });

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        beneficiaries: updatedBeneficiaries,
      },
    ],
  };
}
