// @flow
import { InvalidModelError, InconsistentArgumentError, InvalidUpdateError } from '../../errors';
import { getProgramme } from '../Programme';

// Flow Types
import type { Model } from '../../types';

/**
 * Returns the element at the provided location in the matrix (referenced by id)
 * @param {Model} model model instance
 * @param {Array<string>} location array of string ids indicating location within matrix
 * @return {number} entry at location
 **/
export function getBenefitBuildupEntry(model: Model, location: Array<string>): number {
  const programme = getProgramme(model);
  if (!programme.benefitBuildup) {
    throw new InvalidModelError("The programme did not contain the 'benefitBuildup' attribute");
  } else if (!programme.benefitBuildup[location[0]]) {
    throw new InconsistentArgumentError(
      `The programme 'benefitBuildup' did not contain a key for ${location[0]}`
    );
  } else if (!programme.benefitBuildup[location[0]][location[1]]) {
    throw new InconsistentArgumentError(
      `The programme 'benefitBuildup' did not contain the secondary key ${location[1]}`
    );
  } else if (typeof programme.benefitBuildup[location[0]][location[1]] === 'number') {
    throw new InvalidModelError(`Entry at location ${location[0]}-${location[1]} was not a number`);
  } else if (Number.isNaN(programme.benefitBuildup[location[0]][location[1]])) {
    throw new InvalidModelError(
      `Entry at location ${location[0]}-${location[1]} was NaN (not a number)`
    );
  }

  return programme.benefitBuildup[location[0]][location[1]];
}

/**
 * Update the benefit buildup in the model with a type-checked set of new values
 * @param {Model} model model instance
 * @param {Matrix2D} values values to update with
 * @return {Model} updated model instance
 */
export function setBenefitBuildup(model: Model, values: { [string]: { [string]: number } }): Model {
  // Throw error if values is undefined
  if (!values) {
    throw new InvalidUpdateError('Tried to update benefit buildup with undefined values');
  }

  // Get programme instance
  const programme = getProgramme(model);

  // Throw error if benefitBuildup not defined
  if (!programme.benefitBuildup) {
    throw new InvalidModelError("The programme did not contain the 'benefitBuildup' attribute");
  }

  // Extract benefit ids from provided values
  const benefitIds = Object.keys(values);
  const benefitIdsValid = benefitIds.reduce(
    (acc, id) => acc && programme.benefits.map(b => b.id).includes(id),
    true
  );
  if (!benefitIdsValid || benefitIds.length !== programme.benefits.length) {
    throw new InvalidUpdateError('The benefit keys in the provided update values were invalid');
  }

  // Extract phase ids from provided values
  const phaseIds = Object.keys(values[benefitIds[0]]);
  const phaseIdsValid = phaseIds.reduce(
    (acc, id) => acc && programme.phases.map(p => p.id).includes(id),
    true
  );
  if (!phaseIdsValid || phaseIds.length !== programme.phases.length) {
    throw new InvalidUpdateError('The phase keys in the provided update values were invalid');
  }

  // Create a new object which we will insert type checked values
  const newMatrix = {};

  // Loop each benefit id...
  benefitIds.forEach(bId => {
    // Declare an object to store benefit data in
    const newBenefitObject = {};

    phaseIds.forEach(pId => {
      // If value is properly defined and a number update matrix
      if (
        typeof values[bId][pId] === 'number' &&
        !Number.isNaN(values[bId][pId]) &&
        values[bId][pId] >= 0
      ) {
        // Set benefit object phase key to values
        newBenefitObject[pId] = values[bId][pId];
      } else if (!values[bId][pId]) {
        // Throw error if phase object not defined for this benefit
        throw new InvalidUpdateError(
          'Tried to update benefit buildup when values did not have the correct phase keys'
        );
      } else if (typeof values[bId][pId] !== 'number' || Number.isNaN(values[bId][pId])) {
        // Throw error if value was not a number (NaN or different type)
        throw new InvalidUpdateError(
          'Tried to update benefit buildup when some values were not a number'
        );
      } else {
        throw new InvalidUpdateError(
          'Tried to update benefit buildup when some values were less than zero'
        );
      }
    });

    // Update the matrix object with the benefit object
    newMatrix[bId] = newBenefitObject;
  });

  // Finally, return new model instance with benefit buildup swapped out
  return {
    ...model,
    programmes: [
      {
        ...programme,
        benefitBuildup: newMatrix,
      },
    ],
  };
}
