// @flow
// ID Generator
import nanoId from 'nanoid';

// Date Time Library
import moment from 'moment';

// API Functions
import { getProgramme, getProgrammeAttributes } from '../Programme';
import { getBenefits } from '../Benefit';
import { getDeliverables } from '../Deliverable';
import { getCosts } from '../Cost';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Constants
import {
  DATE_FORMAT,
  PHASE_SLIP_RULE_TIME_AND_MATERIALS,
  PHASE_SLIP_RULE_FIXED,
  COST_TYPE_DEVELOPMENT,
  COST_TYPE_SUPPORT,
  PHASE_LINK_TYPES,
} from '../../../../config/constants';

// Flow Types
import type { Phase, PhaseData, Model, PhaseLink } from '../../types';

type PhaseLinkType = 'START_TO_START' | 'START_TO_FINISH' | 'FINISH_TO_START' | 'FINISH_TO_FINISH';

let newCount = 0;

/**
 * Returns the array of phases from the programme instance
 * @param {Model} model model instance
 * @return {Array<Phase>} array of phases
 **/
export function getPhases(model: Model): Array<Phase> {
  const programme = getProgramme(model);
  if (!programme.phases) {
    throw new InvalidModelError('Programme did not contain an array of phases');
  } else if (!Array.isArray(programme.phases)) {
    throw new InvalidModelError("Programme 'phases' was not an array");
  }
  return programme.phases;
}

/**
 * Returns a specific phase from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id phase id
 * @return {Phase} phase with provided id
 **/
export function getPhaseById(model: Model, id: string): Phase {
  const phases = getPhases(model);
  const found = phases.find(phase => phase.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find phase in programme with id ${id}`);
  }

  return found;
}

function checkPhaseData(phase: PhaseData, model: Model): boolean {
  if (phase.hasOwnProperty('name')) {
    if (typeof phase.name !== 'string' || phase.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided phase update value name was not a string (or it was empty)'
      );
    }
  }
  if (phase.hasOwnProperty('slip')) {
    if (
      typeof phase.slip !== 'string' ||
      ![PHASE_SLIP_RULE_FIXED, PHASE_SLIP_RULE_TIME_AND_MATERIALS].includes(phase.slip)
    ) {
      throw new InvalidUpdateError(
        'Provided phase update value slip was not a string (or it was not a predefined type)'
      );
    }
  }
  if (phase.hasOwnProperty('duration')) {
    if (typeof phase.duration !== 'number' || Number.isNaN(phase.duration) || phase.duration < 0) {
      throw new InvalidUpdateError(
        'Provided phase update value duration was not a number (or it was negative)'
      );
    }
  }
  if (phase.hasOwnProperty('start')) {
    // Construct moment from start date
    const startMoment = moment(phase.start, DATE_FORMAT, true);

    // Get programme start as moment
    const programmeStart = getProgrammeAttributes(model).start;

    if (typeof phase.start !== 'string' || !startMoment.isValid()) {
      throw new InvalidUpdateError(
        'Provided phase update value start was not a string (or it was not in valid format)'
      );
    } else if (startMoment.isBefore(programmeStart)) {
      throw new InvalidUpdateError('Provided phase start date would be before programme start');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new phase (with data if provided)
 * @param {Model} model model instance
 * @param {PhaseData} phase optional phase data to store as phase
 * @return {Model} updated model instance
 */
export function newPhase(model: Model, phase?: PhaseData, id?: string): Model {
  const newPhaseId = id || nanoId();

  // Default new phase
  let phaseData = {
    id: newPhaseId,
    name: `New Phase ${++newCount}`,
    slip: 'TIME_AND_MATERIALS_PHASE',
    duration: 12,
    start: '2020-01-01',
  };

  if (phase) {
    // Check validity of phase data
    checkPhaseData(phase, model);

    // Override default phase data
    phaseData = { ...phaseData, ...phase };
  }

  // Update benefit attribution, cost attribution, and benefit buildup
  const { benefitAttribution, costAttribution, benefitBuildup } = getProgramme(model);
  const [benefits, deliverables, costs] = [
    getBenefits(model),
    getDeliverables(model),
    getCosts(model),
  ];

  const newBenefitAttribution = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = deliverables.reduce((acc, deliverable) => {
      acc[deliverable.id] = {
        ...benefitAttribution[benefit.id][deliverable.id],
        [newPhaseId]: 0,
      };
      return acc;
    }, {});
    return acc;
  }, {});

  const newCostAttribution = costs.reduce((acc, cur) => {
    acc[cur.id] = {
      ...costAttribution[cur.id],
      [newPhaseId]: 0,
    };
    return acc;
  }, {});

  const newBenefitBuildup = benefits.reduce((acc, cur) => {
    acc[cur.id] = {
      ...benefitBuildup[cur.id],
      [newPhaseId]: 0,
    };
    return acc;
  }, {});

  // Update phase cost sensitivity, benefit buildup sensitivity, and phase sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    phaseCostSensitivity: [COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT].reduce((acc, costId) => {
      acc[costId] = {
        ...scenario.phaseCostSensitivity[costId],
        [newPhaseId]: 0,
      };
      return acc;
    }, {}),
    benefitBuildupSensitivity: benefits.reduce((acc, benefit) => {
      acc[benefit.id] = {
        ...scenario.benefitBuildupSensitivity[benefit.id],
        [newPhaseId]: 0,
      };
      return acc;
    }, {}),
    phaseSensitivity: [
      ...scenario.phaseSensitivity,
      {
        id: newPhaseId,
        start: phaseData.start,
        duration: phaseData.duration,
        switch: true,
      },
    ],
  }));

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        phases: [...model.programmes[0].phases, phaseData],
        benefitAttribution: newBenefitAttribution,
        costAttribution: newCostAttribution,
        benefitBuildup: newBenefitBuildup,
        scenarios: newScenarios,
      },
    ],
  };
}

/**
 * Removes a phase with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id phase id
 * @returns {Model} updated model instance
 */
export function removePhaseById(model: Model, id: string): Model {
  if (!model.programmes[0].phases.find(p => p.id === id)) {
    throw new InconsistentArgumentError(
      `No phase with id '${id}' was contained in the model instance`
    );
  }

  // Update benefit attribution and benefit buildup
  const updatedPhases = getPhases(model).filter(p => p.id !== id);
  const {
    benefitAttribution,
    benefits,
    deliverables,
    benefitBuildup,
    costs,
    costAttribution,
  } = getProgramme(model);
  const newBenefitAttribution = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = deliverables.reduce((acc, deliverable) => {
      acc[deliverable.id] = updatedPhases.reduce((acc, phase) => {
        acc[phase.id] = benefitAttribution[benefit.id][deliverable.id][phase.id];
        return acc;
      }, {});
      return acc;
    }, {});
    return acc;
  }, {});

  const newBenefitBuildup = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = updatedPhases.reduce((acc, phase) => {
      acc[phase.id] = benefitBuildup[benefit.id][phase.id];
      return acc;
    }, {});
    return acc;
  }, {});

  const newCostAttribution = costs.reduce((acc, cost) => {
    acc[cost.id] = updatedPhases.reduce((acc, phase) => {
      acc[phase.id] = costAttribution[cost.id][phase.id];
      return acc;
    }, {});
    return acc;
  }, {});

  // Update benefit buildup sensitivity, phase cost sensitivity, and phase sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    phaseCostSensitivity: [COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT].reduce((acc, costId) => {
      acc[costId] = updatedPhases.reduce((acc, phase) => {
        acc[phase.id] = scenario.phaseCostSensitivity[costId][phase.id];
        return acc;
      }, {});
      return acc;
    }, {}),
    benefitBuildupSensitivity: benefits.reduce((acc, benefit) => {
      acc[benefit.id] = updatedPhases.reduce((acc, phase) => {
        acc[phase.id] = scenario.benefitBuildupSensitivity[benefit.id][phase.id];
        return acc;
      }, {});
      return acc;
    }, {}),
    phaseSensitivity: scenario.phaseSensitivity.filter(ps => ps.id !== id),
  }));

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        phases: model.programmes[0].phases.filter(p => p.id !== id),
        benefitAttribution: newBenefitAttribution,
        benefitBuildup: newBenefitBuildup,
        costAttribution: newCostAttribution,
        scenarios: newScenarios,
        phaseLinks: model.programmes[0].phaseLinks.filter(
          l => ![l.sourceId, l.targetId].includes(id)
        ),
      },
    ],
  };
}

/**
 * Sets a phase's attributes by id
 * @param {Model} model model instance
 * @param {string} id phase id
 * @param {PhaseData} phase new phase
 * @return {Model} updated model instance
 */
export function updatePhaseById(model: Model, id: string, phase: PhaseData): Model {
  // Get phase by id to ensure it exists
  getPhaseById(model, id);

  // Check validity of data
  checkPhaseData(phase, model);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        phases: model.programmes[0].phases.map(p =>
          p.id === id
            ? {
                ...p,
                name: phase.hasOwnProperty('name') ? phase.name : p.name,
                slip: phase.hasOwnProperty('slip') ? phase.slip : p.slip,
                duration: phase.hasOwnProperty('duration') ? phase.duration : p.duration,
                start: phase.hasOwnProperty('start') ? phase.start : p.start,
              }
            : p),
      },
    ],
  };
}

export function getPhaseLinks(model: Model): Array<PhaseLink> {
  // Get programme from model
  const programme = getProgramme(model);

  // Check type of phase links array
  if (!programme.phaseLinks) {
    throw new InvalidModelError('Phase links key was not present in programme');
  } else if (!Array.isArray(programme.phaseLinks)) {
    throw new InvalidModelError('Phase links key was not an array in programme');
  }

  // Return phase links
  return programme.phaseLinks;
}

export function addPhaseLink(
  model: Model,
  id: number,
  type: PhaseLinkType,
  sourceId: string,
  targetId: string
): Model {
  // Get programme form model
  const programme = getProgramme(model);

  // Check source and target ids exist by retreiving respective phases
  getPhaseById(model, sourceId);
  getPhaseById(model, targetId);

  // Check phase id is a number
  if (typeof id !== 'number' || Number.isNaN(id)) {
    throw new InvalidUpdateError('Phase link ids should be a number');
  }

  // Check phase type
  if (!PHASE_LINK_TYPES.includes(type)) {
    throw new InvalidUpdateError('Phase link type was not one of the predefined types');
  }

  const newLink = {
    id,
    type,
    sourceId,
    targetId,
  };

  return {
    ...model,
    programmes: [
      {
        ...programme,
        phaseLinks: [...programme.phaseLinks, newLink],
      },
    ],
  };
}

export function deletePhaseLink(model: Model, id: number): Model {
  const programme = getProgramme(model);

  if (!programme.phaseLinks.find(link => link.id === id)) {
    throw new InconsistentArgumentError('Could not find phase link with provided id');
  }

  return {
    ...model,
    programmes: [
      {
        ...programme,
        phaseLinks: programme.phaseLinks.filter(link => link.id !== id),
      },
    ],
  };
}
