// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { getDeliverables } from '../Deliverable';
import { getPhases } from '../Phase';
import { getBenefits } from '../Benefit';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Constants
import { COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT } from '../../../../config/constants';

// Flow Types
import type { Scenario, ScenarioData, Model } from '../../types';

let newCount = 0;

/**
 * Returns the array of scenarios from the programme instance
 * @param {Model} model model instance
 * @return {Array<Scenario>} array of scenarios
 **/
export function getScenarios(model: Model): Array<Scenario> {
  const programme = getProgramme(model);
  if (!programme.scenarios) {
    throw new InvalidModelError('Programme did not contain an array of scenarios');
  } else if (!Array.isArray(programme.scenarios)) {
    throw new InvalidModelError("Programme 'scenarios' was not an array");
  }
  return programme.scenarios;
}

/**
 * Returns a specific scenario from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id scenario id
 * @return {Scenario} scenario with provided id
 **/
export function getScenarioById(model: Model, id: string): Scenario {
  const scenarios = getScenarios(model);
  const found = scenarios.find(scenario => scenario.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find scenario in programme with id ${id}`);
  }

  return found;
}

function checkScenarioData(scenario: ScenarioData): boolean {
  if (scenario.hasOwnProperty('name')) {
    if (typeof scenario.name !== 'string' || scenario.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided scenario update value name was not a string (or it was empty)'
      );
    }
  }
  if (scenario.hasOwnProperty('definition')) {
    if (typeof scenario.definition !== 'string') {
      throw new InvalidUpdateError('Provided scenario update value definition was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new scenario (with data if provided)
 * @param {Model} model model instance
 * @param {ScenarioData} scenario optional scenario data to store as scenario
 * @return {Model} updated model instance
 */
export function newScenario(model: Model, scenario?: ScenarioData, id?: string): Model {
  const newScenarioId = id || nanoId();
  // Default new scenario
  let scenarioData = {
    id: newScenarioId,
    name: `New Scenario ${++newCount}`,
    definition: '',
  };

  if (scenario) {
    // Check validity of benefit data
    checkScenarioData(scenario);

    // Override default benefit data
    scenarioData = { ...scenarioData, ...scenario };
  }

  const phases = getPhases(model);
  const phaseSensitivity = phases.map(phase => ({
    id: phase.id,
    duration: phase.duration,
    start: phase.start,
    switch: true,
  }));

  const deliverables = getDeliverables(model);
  const deliverableSensitivity = deliverables.map(deliverable => ({
    id: deliverable.id,
    switch: true,
  }));

  const benefits = getBenefits(model);
  const benefitSensitivity = benefits.map(benefit => ({
    id: benefit.id,
    switch: true,
    sensitivity: 0,
  }));

  const costTypes = [COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT];
  const phaseCostSensitivity = costTypes.reduce((costAcc, curCost) => {
    costAcc[curCost] = phases.reduce((phaseAcc, curPhase) => {
      phaseAcc[curPhase.id] = 0;
      return phaseAcc;
    }, {});
    return costAcc;
  }, {});

  const benefitBuildupSensitivity = benefits.reduce((benefitAcc, curBenefit) => {
    benefitAcc[curBenefit.id] = phases.reduce((phaseAcc, curPhase) => {
      phaseAcc[curPhase.id] = 0;
      return phaseAcc;
    }, {});
    return benefitAcc;
  }, {});

  scenarioData = {
    ...scenarioData,
    phaseSensitivity,
    deliverableSensitivity,
    benefitSensitivity,
    phaseCostSensitivity,
    benefitBuildupSensitivity,
  };

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        scenarios: [...model.programmes[0].scenarios, scenarioData],
      },
    ],
  };
}

/**
 * Removes a scenario with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id scenario id
 * @returns {Model} updated model instance
 */
export function removeScenarioById(model: Model, id: string): Model {
  // Check model exists, core benefits exists, and whether to remove is core benefit
  if (!model.programmes[0].scenarios.find(s => s.id === id)) {
    throw new InconsistentArgumentError(
      `No scenario with id '${id}' was contained in the model instance`
    );
  }

  // Return modified model with scenario removed
  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        scenarios: model.programmes[0].scenarios.filter(s => s.id !== id),
      },
    ],
  };
}

/**
 * Sets a scenario's attributes by id
 * @param {Model} model model instance
 * @param {string} id scenario id
 * @param {ScenarioData} scenario new scenario
 * @return {Model} updated model instance
 */
export function updateScenarioById(model: Model, id: string, scenario: ScenarioData): Model {
  // Get scenario by id to confirm it exists
  getScenarioById(model, id);

  // Check validity of data
  checkScenarioData(scenario);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        scenarios: model.programmes[0].scenarios.map(s =>
          s.id === id
            ? {
                ...s,
                name: scenario.hasOwnProperty('name') ? scenario.name : s.name,
                definition: scenario.hasOwnProperty('definition')
                  ? scenario.definition
                  : s.definition,
              }
            : s),
      },
    ],
  };
}
