export function getFormFieldLabel(id: string, key: string): string {
  // Retreive form object with id
  const form = getFormObject(id);

  // Find field with name which matches key
  const found = form.fields.find(field => {
    return field.name === key;
  });

  // If not found throw error
  if (!found) {
    throw new Error(`Could not find field associated with ${id} and ${key}`);
  }

  // Return label of found field
  return found.label;
}

export function isElementId(key: string, attribute: string): boolean {
  const idAttributes = [
    'benefitId',
    'deliverableId',
    'measureId',
    'objectiveId',
    'perspectiveId',
    'stakeholderId',
    'beneficiaryId',
    'groupId',
  ];
  return idAttributes.includes(attribute);
}

export function getSelectElementLabel(programme: types.Programme, id: string): string {
  // Return none label if none selected
  if (id === 'none') {
    return 'None';
  }

  // TODO: Review this implementation, seems whack
  const modelTypes = [...CONSTANTS.ATOMIC_TYPES, ...CONSTANTS.STRATEGY_MAP_TYPES];

  // Match first instance of id from all possible types
  for (let i = 0; i < modelTypes.length; i++) {
    // Get elements for type
    const elements = programme[modelTypes[i].key];
    // Try and find matching element
    const found = elements.find(element => element.id === id);

    // If found and there is an attached name return it
    if (found && found.name) return found.name;
  }

  // If exhaust all modelTypes and cannot find
  throw new Error(`Could not find element with id ${id}`);
}

export function getMeasuresStringFromIdArray(
  programme: types.Programme,
  measureIds: Array<string>
): string {
  const { measures } = programme;
  let strRep = '';
  measureIds.forEach((id, idx) => {
    const found = measures.find(measure => measure.id === id);
    if (!found) {
      throw new Error(`Could not find measure with id ${id}`);
    }

    strRep += found.name;

    if (idx !== measureIds.length - 1) {
      strRep += ', ';
    }
  });

  return strRep;
}

