const matchesId = (id: string) => (element: types.ModelElement): boolean => element.id === id;

export function getExplorerPathIndex(
  programme: types.Programme,
  id: string,
  type: TypeObject
): Array<number> {
  switch (type.id) {
    case CONSTANTS.BENEFIT_ID:
      return [0, programme.benefits.findIndex(matchesId(id))];
    case CONSTANTS.COST_ID:
      return [1, programme.costs.findIndex(matchesId(id))];
    case CONSTANTS.DELIVERABLE_ID:
      return [2, programme.deliverables.findIndex(matchesId(id))];
    case CONSTANTS.STAKEHOLDER_ID:
      return [3, programme.stakeholders.findIndex(matchesId(id))];
    case CONSTANTS.PHASE_ID:
      return [4, programme.phases.findIndex(matchesId(id))];
    case CONSTANTS.GROUP_ID:
      return [5, programme.groups.findIndex(matchesId(id))];
    case CONSTANTS.BENEFICIARY_ID:
      return [6, programme.beneficiaries.findIndex(matchesId(id))];
    case CONSTANTS.PARAMETER_ID:
      return [7, programme.parameters.findIndex(matchesId(id))];
    case CONSTANTS.CAUSE_AND_EFFECT_ID:
      return [8, programme.causeAndEffect.findIndex(matchesId(id))];
    case CONSTANTS.PERSPECTIVE_ID:
      return [9, programme.perspectives.findIndex(matchesId(id))];
    case CONSTANTS.OBJECTIVE_ID:
      return [10, programme.objectives.findIndex(matchesId(id))];
    case CONSTANTS.MEASURE_ID:
      return [11, programme.measures.findIndex(matchesId(id))];
    case CONSTANTS.THEME_ID:
      return [12, programme.themes.findIndex(matchesId(id))];
    default:
      throw new Error(`Could not construct explorer path index for ${id}`);
  }
}

export function getDeletionConfirmationText(typeId: string): Array<string> {
  switch (typeId) {
    case CONSTANTS.BENEFIT_ID:
    case CONSTANTS.DELIVERABLE_ID:
    case CONSTANTS.MEASURE_ID:
      return [CONSTANTS.DELETE_CONFIRM_TEXT, CONSTANTS.DELETE_THREAD_DISCLAIMER];
    case CONSTANTS.PERSPECTIVE_ID:
      return [
        CONSTANTS.DELETE_CONFIRM_TEXT,
        CONSTANTS.DELETE_PERSPECTIVE_DISCLAIMER,
        CONSTANTS.DELETE_STRATEGY_THREAD_DISCLAIMER,
      ];
    case CONSTANTS.OBJECTIVE_ID:
      return [
        CONSTANTS.DELETE_CONFIRM_TEXT,
        CONSTANTS.DELETE_OBJECTIVE_DISCLAIMER,
        CONSTANTS.DELETE_STRATEGY_THREAD_DISCLAIMER,
      ];
    case CONSTANTS.COST_ID:
    case CONSTANTS.PARAMETER_ID:
    case CONSTANTS.STAKEHOLDER_ID:
    case CONSTANTS.PHASE_ID:
    case CONSTANTS.GROUP_ID:
    case CONSTANTS.BENEFICIARY_ID:
    case CONSTANTS.CAUSE_AND_EFFECT_ID:
    default:
      return [CONSTANTS.DELETE_CONFIRM_TEXT];
  }
}

export function getDefaultNewElement(
  programme: types.Programme,
  typeId: string
): { [string]: any } {
  switch (typeId) {
    case CONSTANTS.BENEFIT_ID:
      return {
        name: `Benefit ${programme[CONSTANTS.BENEFIT_KEY].length + 1}`,
        unitOfMeasure: programme.unitOfMeasure,
        type: CONSTANTS.BENEFIT_TYPE_FINANCIAL,
      };
    case CONSTANTS.DELIVERABLE_ID:
      return {
        name: `Deliverable ${programme[CONSTANTS.DELIVERABLE_KEY].length + 1}`,
        unitOfMeasure: programme.unitOfMeasure,
      };
    case CONSTANTS.MEASURE_ID:
      return {
        name: `Measure ${programme[CONSTANTS.MEASURE_KEY].length + 1}`,
        weighting: 10,
        objectiveId: programme[CONSTANTS.OBJECTIVE_KEY][0], // Objective always added first if none
        unitOfMeasure: programme.unitOfMeasure,
      };
    case CONSTANTS.PERSPECTIVE_ID:
      return {
        name: `Perspective ${programme[CONSTANTS.PERSPECTIVE_KEY].length + 1}`,
        weighting: 25,
      };
    case CONSTANTS.OBJECTIVE_ID:
      return {
        name: `Objective ${programme[CONSTANTS.OBJECTIVE_KEY].length + 1}`,
        weighting: 25,
        perspectiveId: programme[CONSTANTS.PERSPECTIVE_KEY][0], // Perspective always added first
      };
    case CONSTANTS.COST_ID:
      return {
        name: `Cost ${programme[CONSTANTS.COST_KEY].length + 1}`,
        type: CONSTANTS.COST_TYPE_DEVELOPMENT,
      };
    case CONSTANTS.PARAMETER_ID:
      return {
        name: `p_parameter${programme[CONSTANTS.PARAMETER_KEY].length + 1}`,
        value: 0,
        unitOfMeasure: '%',
        change: 0,
        changeType: CONSTANTS.PARAMETER_CHANGE_TYPE_ARITHMETIC,
      };
    case CONSTANTS.STAKEHOLDER_ID:
      return {
        name: `Stakeholder ${programme[CONSTANTS.STAKEHOLDER_KEY].length + 1}`,
      };
    case CONSTANTS.PHASE_ID:
      return {
        name: `Phase ${programme[CONSTANTS.PHASE_KEY].length + 1}`,
        duration: 4,
        start: programme.start,
        slip: CONSTANTS.PHASE_SLIP_RULE_TIME_AND_MATERIALS,
      };
    case CONSTANTS.GROUP_ID:
      return {
        name: `Group ${programme[CONSTANTS.GROUP_KEY].length + 1}`,
      };
    case CONSTANTS.BENEFICIARY_ID:
      return {
        name: `Beneficiary ${programme[CONSTANTS.BENEFICIARY_KEY].length + 1}`,
      };
    case CONSTANTS.CAUSE_AND_EFFECT_ID:
      return {
        deliverableId:
          programme[CONSTANTS.DELIVERABLE_KEY][0] && programme[CONSTANTS.DELIVERABLE_KEY][0].id,
        benefitId: programme[CONSTANTS.BENEFIT_KEY][0] && programme[CONSTANTS.BENEFIT_KEY][0].id,
        measureId: programme[CONSTANTS.MEASURE_KEY][0] && programme[CONSTANTS.MEASURE_KEY][0].id,
      };
    default:
      throw new Error(`Could not retreive default new object for type ${typeId}`);
  }
}

let refs = 22;
export function nextRef(): string {
  return `${CONSTANTS.CAUSE_AND_EFFECT_REFERENCE_PREFIX}${++refs}`;
}

export const downloadModel = (model: types.Model) => {
  // Define data string by stringifying export object
  const dataStr = `data:text/json;charset=utf-8,${encodeURIComponent(
    JSON.stringify(model, null, '  ')
  )}`;
  // Create invisible anchor node to trigger download
  const downloadAnchorNode = document.createElement('a');

  // Set string as payload
  downloadAnchorNode.setAttribute('href', dataStr);

  // Set name as argument value or if undefined default new model name
  downloadAnchorNode.setAttribute('download', `${model.name}.vmd`);

  // Append anchor to body as firefox will not trigger events unmounted
  if (document.body) {
    document.body.appendChild(downloadAnchorNode); // required for firefox
  }

  // Simulate click event and remove anchor
  downloadAnchorNode.click();
  downloadAnchorNode.remove();
};
