// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { getBenefits } from '../Benefit';
import { getPhases } from '../Phase';
import { removeThreadById } from '../Thread';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Deliverable, DeliverableData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of deliverables from the programme instance
 * @param {Model} model model instance
 * @return {Array<Deliverable>} array of deliverables
 **/
export function getDeliverables(model: Model): Array<Deliverable> {
  const programme = getProgramme(model);
  if (!programme.deliverables) {
    throw new InvalidModelError('Programme did not contain an array of deliverables');
  } else if (!Array.isArray(programme.deliverables)) {
    throw new InvalidModelError("Programme 'deliverables' was not an array");
  }
  return programme.deliverables;
}

/**
 * Returns a specific deliverable from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id deliverable id
 * @return {Deliverable} deliverable with provided id
 **/
export function getDeliverableById(model: Model, id: string): Deliverable {
  const deliverables = getDeliverables(model);
  const found = deliverables.find(deliverable => deliverable.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find deliverable in programme with id ${id}`);
  }

  return found;
}

function checkDeliverableData(deliverable: DeliverableData): boolean {
  if (deliverable.hasOwnProperty('name')) {
    if (typeof deliverable.name !== 'string' || deliverable.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided deliverable update value name was not a string (or it was empty)'
      );
    }
  }
  if (deliverable.hasOwnProperty('definition')) {
    if (typeof deliverable.definition !== 'string') {
      throw new InvalidUpdateError('Provided deliverable update value definition was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new deliverable (with data if provided)
 * @param {Model} model model instance
 * @param {DeliverableData} deliverable optional deliverable data to store as deliverable
 * @return {Model} updated model instance
 */
export function newDeliverable(model: Model, deliverable?: DeliverableData, id?: string): Model {
  const newDeliverableId = id || nanoId();
  // Default new deliverable
  let deliverableData = {
    id: newDeliverableId,
    name: `New Deliverable ${++newCount}`,
    definition: '',
  };

  if (deliverable) {
    // Check validity of deliverable data
    checkDeliverableData(deliverable);

    // Override default deliverable data
    deliverableData = { ...deliverableData, ...deliverable };
  }

  // Update Benefit Attribution
  const { benefitAttribution } = getProgramme(model);

  const benefits = getBenefits(model);
  const phases = getPhases(model);
  const newBenefitAttribution = benefits.reduce((acc, cur) => {
    acc[cur.id] = {
      ...benefitAttribution[cur.id],
      [newDeliverableId]: phases.reduce((acc, cur) => {
        acc[cur.id] = 0;
        return acc;
      }, {}),
    };
    return acc;
  }, {});

  // Update deliverable sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    deliverableSensitivity: [
      ...scenario.deliverableSensitivity,
      {
        id: newDeliverableId,
        switch: true,
      },
    ],
  }));

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        deliverables: [...model.programmes[0].deliverables, deliverableData],
        benefitAttribution: newBenefitAttribution,
        scenarios: newScenarios,
      },
    ],
  };
}

/**
 * Removes a deliverable with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id deliverable id
 * @returns {Model} updated model instance
 */
export function removeDeliverableById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'cascade'
): Model {
  if (!model.programmes[0].deliverables.find(d => d.id === id)) {
    throw new InconsistentArgumentError(
      `No deliverable with id '${id}' was contained in the model instance`
    );
  }

  // Update cause and effects
  let modifiedModel;
  switch (onDelete) {
    case 'cascade':
      const { causeAndEffect } = getProgramme(model);
      modifiedModel = causeAndEffect
        .filter(t => t.deliverableId === id)
        .reduce((acc, cur) => removeThreadById(acc, cur.id), model);
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when removing deliverable');
  }

  // Update benefit attribution
  const updatedDeliverables = getDeliverables(model).filter(d => d.id !== id);
  const { benefitAttribution, benefits, phases } = getProgramme(model);
  const newBenefitAttribution = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = updatedDeliverables.reduce((acc, deliverable) => {
      acc[deliverable.id] = phases.reduce((acc, phase) => {
        acc[phase.id] = benefitAttribution[benefit.id][deliverable.id][phase.id];
        return acc;
      }, {});
      return acc;
    }, {});
    return acc;
  }, {});

  // Update deliverable sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    deliverableSensitivity: scenario.deliverableSensitivity.filter(ds => ds.id !== id),
  }));

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        deliverables: modifiedModel.programmes[0].deliverables.filter(d => d.id !== id),
        benefitAttribution: newBenefitAttribution,
        scenarios: newScenarios,
      },
    ],
  };
}

/**
 * Sets a deliverable's attributes by id
 * @param {Model} model model instance
 * @param {string} id deliverable id
 * @param {DeliverableData} deliverable new deliverable
 * @return {Model} updated model instance
 */
export function updateDeliverableById(
  model: Model,
  id: string,
  deliverable: DeliverableData
): Model {
  const found = model.programmes[0].deliverables.find(d => d.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No deliverable with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkDeliverableData(deliverable);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        deliverables: model.programmes[0].deliverables.map(d =>
          d.id === id
            ? {
                ...d,
                name: deliverable.hasOwnProperty('name') ? deliverable.name : d.name,
                definition: deliverable.hasOwnProperty('definition')
                  ? deliverable.definition
                  : d.definition,
              }
            : d),
      },
    ],
  };
}
