// Action types
import * as actions from '../actions/types';

// Configuration constants and model API functions
import * as CONSTANTS from '../config/constants';
import * as API from '../model/api';

// Import initial model and schema
// import BLANK from '../model/examples/blank.json';
// import JANET_MODEL from '../model/examples/janet.json';
import MIDSIZE_BANK_MODEL from '../model/examples/midsize.json';
// import HOSPITAL from '../model/examples/hospital.json';

// Import functions for checking and safely updating model
import safetyCheck from '../model/api/validation/SafeUpdate';
import confirmValid from '../model/api/validation';
import randomValues from '../utils/createRandomScorecardValues';

// Validate initial model schema
// confirmValid(BLANK, 'load');
// confirmValid(JANET_MODEL, 'load');
confirmValid(MIDSIZE_BANK_MODEL, 'load');
// confirmValid(HOSPITAL, 'load');

// Set initial state to model
// const initialState = BLANK;
// const initialState = JANET;
const initialState = MIDSIZE_BANK_MODEL;
// const initialState = HOSPITAL;

// Reducer
export default (state = initialState, action = {}) => {
  switch (action.type) {
    // Upload model action
    case 'UPLOAD_MODEL':
      return safetyCheck(state)(() => action.model, 'upload');
    // Update model action
    case 'UPDATE_MODEL':
      return safetyCheck(state)(() => action.model, 'update');
    // Update Atom action:
    case actions.UPDATE_ATOM:
      // Filter action key to select appropriate Model API function
      switch (action.key) {
        case CONSTANTS.MODEL_KEY:
          return safetyCheck(state)(() => API.updateModel(state, action.values), 'update');
        case CONSTANTS.PROGRAMME_KEY:
          return safetyCheck(state)(() => API.updateProgramme(state, action.values), 'update');
        case CONSTANTS.SCENARIO_KEY:
          return safetyCheck(state)(
            () => API.updateScenarioById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.BENEFIT_KEY:
          return safetyCheck(state)(
            () => API.updateBenefitById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.COST_KEY:
          return safetyCheck(state)(
            () => API.updateCostById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.DELIVERABLE_KEY:
          return safetyCheck(state)(
            () => API.updateDeliverableById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.PHASE_KEY:
          return safetyCheck(state)(
            () => API.updatePhaseById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.STAKEHOLDER_KEY:
          return safetyCheck(state)(
            () => API.updateStakeholderById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.GROUP_KEY:
          return safetyCheck(state)(
            () => API.updateGroupById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.BENEFICIARY_KEY:
          return safetyCheck(state)(
            () => API.updateBeneficiaryById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.CAUSE_AND_EFFECT_KEY:
          return safetyCheck(state)(
            () => API.updateThreadById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.PARAMETER_KEY:
          return safetyCheck(state)(
            () => API.updateParameterById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.MEASURE_KEY:
          return safetyCheck(state)(
            () => API.updateMeasureById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.OBJECTIVE_KEY:
          return safetyCheck(state)(
            () => API.updateObjectiveById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.PERSPECTIVE_KEY:
          return safetyCheck(state)(
            () => API.updatePerspectiveById(state, action.id, action.values),
            'update'
          );
        case CONSTANTS.THEME_KEY:
          return safetyCheck(state)(
            () => API.updateThemeById(state, action.id, action.values),
            'update'
          );
        default:
          throw new Error('Unrecognised type key when updating atom');
      }
    // Delete atom action
    case actions.DELETE_ATOM:
      switch (action.key) {
        case CONSTANTS.BENEFIT_KEY:
          return safetyCheck(state)(
            () => API.removeBenefitById(state, action.id, 'cascade'),
            'delete'
          );
        case CONSTANTS.COST_KEY:
          return safetyCheck(state)(() => API.removeCostById(state, action.id), 'delete');
        case CONSTANTS.DELIVERABLE_KEY:
          return safetyCheck(state)(
            () => API.removeDeliverableById(action.id, 'cascade'),
            'delete'
          );
        case CONSTANTS.PHASE_KEY:
          return safetyCheck(state)(() => API.removePhaseById(state, action.id), 'delete');
        case CONSTANTS.STAKEHOLDER_KEY:
          return safetyCheck(state)(
            () => API.removeStakeholderById(state, action.id, 'cascade'),
            'delete'
          );
        case CONSTANTS.GROUP_KEY:
          return safetyCheck(state)(() => API.removeGroupById(state, action.id, 'null'), 'delete');
        case CONSTANTS.BENEFICIARY_KEY:
          return safetyCheck(state)(
            () => API.removeBeneficiaryById(state, action.id, 'null'),
            'delete'
          );
        case CONSTANTS.CAUSE_AND_EFFECT_KEY:
          return safetyCheck(state)(() => API.removeThreadById(state, action.id), 'delete');
        case CONSTANTS.PARAMETER_KEY:
          return safetyCheck(state)(() => API.removeParameterById(state, action.id), 'delete');
        case CONSTANTS.MEASURE_KEY:
          return safetyCheck(state)(
            () => API.removeMeasureById(state, action.id, 'null'),
            'delete'
          );
        case CONSTANTS.OBJECTIVE_KEY:
          return safetyCheck(state)(
            () => API.removeObjectiveById(state, action.id, 'null'),
            'delete'
          );
        case CONSTANTS.PERSPECTIVE_KEY:
          return safetyCheck(state)(
            () => API.removePerspectiveById(state, action.id, 'null'),
            'delete'
          );
        case CONSTANTS.THEME_KEY:
          return safetyCheck(state)(() => API.removeThemeById(state, action.id), 'delete');
        case CONSTANTS.SCENARIO_KEY:
          return safetyCheck(state)(() => API.removeScenarioById(state, action.id), 'delete');
        case CONSTANTS.MODEL_KEY:
        case CONSTANTS.PROGRAMME_KEY:
          throw new Error('Tried to delete model or programme');
        default:
          throw new Error('Unrecognised type key when deleting atom');
      }
    // Add new atom action
    case actions.ADD_ATOM:
      switch (action.key) {
        case CONSTANTS.BENEFIT_KEY:
          return safetyCheck(state)(() => API.newBenefit(state, action.element, action.id), 'add');
        case CONSTANTS.COST_KEY:
          return safetyCheck(state)(() => API.newCost(state, action.element, action.id), 'add');
        case CONSTANTS.DELIVERABLE_KEY:
          return safetyCheck(state)(
            () => API.newDeliverable(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.PHASE_KEY:
          return safetyCheck(state)(() => API.newPhase(state, action.element, action.id), 'add');
        case CONSTANTS.STAKEHOLDER_KEY:
          return safetyCheck(state)(
            () => API.newStakeholder(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.GROUP_KEY:
          return safetyCheck(state)(() => API.newGroup(state, action.element, action.id), 'add');
        case CONSTANTS.BENEFICIARY_KEY:
          return safetyCheck(state)(
            () => API.newBeneficiary(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.CAUSE_AND_EFFECT_KEY:
          return safetyCheck(state)(() => API.newThread(state, action.element, action.id), 'add');
        case CONSTANTS.PARAMETER_KEY:
          return safetyCheck(state)(
            () => API.newParameter(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.MEASURE_KEY:
          return safetyCheck(state)(() => API.newMeasure(state, action.element, action.id), 'add');
        case CONSTANTS.THEME_KEY:
          return safetyCheck(state)(() => API.newTheme(state, action.element, action.id), 'add');
        case CONSTANTS.OBJECTIVE_KEY:
          return safetyCheck(state)(
            () => API.newObjective(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.PERSPECTIVE_KEY:
          return safetyCheck(state)(
            () => API.newPerspective(state, action.element, action.id),
            'add'
          );
        case CONSTANTS.SCENARIO_KEY:
          return safetyCheck(state)(() => API.newScenario(state, action.element, action.id), 'add');
        case CONSTANTS.MODEL_KEY:
        case CONSTANTS.PROGRAMME_KEY:
          throw new Error('Tried to add a new model or programme');
        default:
          throw new Error('Unrecognised type key when adding atom');
      }
    // Update matrix action
    case actions.UPDATE_MATRIX:
      switch (action.key) {
        case CONSTANTS.BENEFIT_ATTRIBUTION_KEY:
          return safetyCheck(state)(
            () => API.setBenefitAttribution(state, action.values),
            'update matrix'
          );
        case CONSTANTS.BENEFIT_BUILDUP_KEY:
          return safetyCheck(state)(
            () => API.setBenefitBuildup(state, action.values),
            'update matrix'
          );
        case CONSTANTS.COST_ATTRIBUTION_KEY:
          return safetyCheck(state)(
            () => API.setCostAttribution(state, action.values),
            'update matrix'
          );
        case CONSTANTS.STAKEHOLDER_BENEFITS_KEY:
          return safetyCheck(state)(
            () => API.setStakeholderBenefits(state, action.values),
            'update matrix'
          );
        default:
          throw new Error('Unrecognised type key when updating matrix');
      }
    // Remove phase link action
    case actions.DELETE_PHASE_LINK:
      return safetyCheck(state)(() => API.deletePhaseLink(state, action.id), 'remove phase link');
    // Add new phase link action
    case actions.ADD_PHASE_LINK:
      return safetyCheck(state)(
        () => API.addPhaseLink(state, action.id, action.linkType, action.sourceId, action.targetId),
        'add phase link'
      );
    // Update Sensitivity values
    case actions.UPDATE_SENSITIVITY:
      switch (action.typeKey) {
        case 'phaseSensitivity':
          return safetyCheck(state)(
            () => API.setPhaseSensitivityValues(state, action.id, action.values),
            'update phase sensitivities'
          );
        case 'benefitSensitivity':
          return safetyCheck(state)(
            () => API.setBenefitSensitivityValues(state, action.id, action.values),
            'update benefit sensitivities'
          );
        case 'deliverableSensitivity':
          return safetyCheck(state)(
            () => API.setDeliverableSensitivityValues(state, action.id, action.values),
            'update deliverable sensitivities'
          );
        case 'benefitBuildupSensitivity':
          return safetyCheck(state)(
            () => API.setBenefitBuildupSensitivityValues(state, action.id, action.values),
            'update benefit buildup sensitivities'
          );
        case 'phaseCostSensitivity':
          return safetyCheck(state)(
            () => API.setPhaseCostSensitivityValues(state, action.id, action.values),
            'update phase cost sensitivities'
          );
        default:
          throw new Error('Unrecognised type key when updating scenario sensitivities');
      }
    // Otherwise return current state
    default:
      return state;
  }
};
