// @flow
// Model Errors
import { InvalidModelError, InvalidUpdateError } from '../../errors';

// Flow Types
import type { Model, ModelAttributes } from '../../types';

// Constants
const modelAttributes = ['id', 'name', 'client', 'author'];

/**
 * Returns the non-array attributes of the provided model instance
 * @param {Model} model model instance
 * @return {ModelAttributes} an object containing all the model's (non-array) attributes
 */
export function getModelAttributes(model: Model): ModelAttributes {
  // Define new object to return
  const attributes = {};

  // For each attribute check it's validity and add to the return object
  modelAttributes.forEach(key => {
    if (!model[key]) {
      throw new InvalidModelError(`Provided model did not contain the '${key}' attribute`);
    } else if (typeof model[key] !== 'string') {
      throw new InvalidModelError(`Provided model's '${key}' attribute was not a string`);
    }

    attributes[key] = model[key];
  });

  // Return new object
  return attributes;
}

function checkModelData(data: ModelAttributes): boolean {
  if (data.hasOwnProperty('name')) {
    if (typeof data.name !== 'string' || data.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided model update values did not contain the name attribute (or it was empty)'
      );
    }
  }
  if (data.hasOwnProperty('client')) {
    if (typeof data.client !== 'string') {
      throw new InvalidUpdateError(
        'Provided model update values did not contain the client attribute'
      );
    }
  }
  if (data.hasOwnProperty('author')) {
    if (typeof data.author !== 'string') {
      throw new InvalidUpdateError(
        'Provided model update values did not contain the author attribute'
      );
    }
  }
  return true;
}

/**
 * Sets model values based on provided values
 * @param {Model} model model instance
 * @param {Model} values model values to update
 */
export function updateModel(model: Model, values: ModelAttributes): Model {
  checkModelData(values);

  return {
    ...model,
    name: values.hasOwnProperty('name') ? values.name : model.name,
    client: values.hasOwnProperty('client') ? values.client : model.client,
    author: values.hasOwnProperty('author') ? values.author : model.author,
  };
}

/**
 * Returns the model id
 * @param  {Model} model model instance
 * @return {string}      model id
 */
export function getModelId(model: Model): string {
  if (!model.id) {
    throw new InvalidModelError("Provided model did not contain the 'id' attribute");
  } else if (typeof model.id !== 'string') {
    throw new InvalidModelError("Provided model's 'id' attribute was not a string");
  }

  return model.id;
}

/**
 * Returns the model name
 * @param  {Model} model model instance
 * @return {string}      model name
 */
export function getModelName(model: Model): string {
  if (!model.name) {
    throw new InvalidModelError("Provided model did not contain the 'name' attribute");
  } else if (typeof model.name !== 'string') {
    throw new InvalidModelError("Provided model's 'name' attribute was not a string");
  }

  return model.name;
}

/**
 * Returns the model client
 * @param  {Model} model model instance
 * @return {string}      model client
 */
export function getModelClient(model: Model): string {
  if (!model.client) {
    throw new InvalidModelError("Provided model did not contain the 'client' attribute");
  } else if (typeof model.client !== 'string') {
    throw new InvalidModelError("Provided model's 'client' attribute was not a string");
  }

  return model.client;
}

/**
 * Returns the model author
 * @param  {Model} model model instance
 * @return {string}      model author
 */
export function getModelAuthor(model: Model): string {
  if (!model.author) {
    throw new InvalidModelError("Provided model did not contain the 'author' attribute");
  } else if (typeof model.author !== 'string') {
    throw new InvalidModelError("Provided model's 'author' attribute was not a string");
  }

  return model.author;
}

/**
 * Sets the model name
 * @param  {Model} model model instance
 * @param  {string} name new model name
 */
export function setModelName(model: Model, name: string): Model {
  if (!name || typeof name !== 'string') {
    throw new InvalidUpdateError(
      `Updating the model name as ${name} is invalid, 'name' must be a string`
    );
  }

  return {
    ...model,
    name,
  };
}

/**
 * Sets the model client
 * @param  {Model} model   model instance
 * @param  {string} client new model client
 */
export function setModelClient(model: Model, client: string): Model {
  if (!client || typeof client !== 'string') {
    throw new InvalidUpdateError(
      `Updating the model client as ${client} is invalid, 'client' must be a string`
    );
  }

  return {
    ...model,
    client,
  };
}

/**
 * Sets the model author
 * @param  {Model} model model instance
 * @param  {string} author new model author
 */
export function setModelAuthor(model: Model, author: string): Model {
  if (!author || typeof author !== 'string') {
    throw new InvalidUpdateError(
      `Updating the model author as ${author} is invalid, 'author' must be a string`
    );
  }

  return {
    ...model,
    author,
  };
}
