// @flow
import React, { Component, Suspense, lazy } from 'react';

// React Router
import { BrowserRouter as Router } from 'react-router-dom';

// React Redux
import { connect } from 'react-redux';

// Import validate authorisation action
import { validate } from './actions/auth';

// Import route components
import AuthorisedRoute from './routes/AuthorisedRoute';
import UnauthorisedRoute from './routes/UnauthorisedRoute';

// Import loading component
import Loading from './components/Loading';
import Home from './views/Home';

// Use App CSS file
import './App.css';

// Flow types
import * as types from './types';
import { type State } from './reducers/types';
import { type Dispatch } from './actions/types';

// Lazily import AppView and Login, to prevent code reaching browser without auth
const Login = lazy(() => import('./views/Login'));
const AppView = lazy(() => import('./views/AppView'));

export class App extends Component<types.AppProps> {
  componentDidMount() {
    // If we believe we are authenticated, validate credentials
    if (this.props.isAuthenticated) {
      this.props.validate();
    }
  }

  render() {
    return (
      <Router>
        <div className="App">
          {/* We have lazily loaded components, so show Loading whilst user waits */}
          <Suspense fallback={<Loading text="Initialising Application..." />}>
            <AppView />
          </Suspense>
        </div>
      </Router>
    );
  }
}

// Redux Mappings
const mapStateToProps = (state: State) => ({
  isAuthenticated: state.auth.isAuthenticated,
  user: state.auth.user,
});

const mapDispatchToProps = (dispatch: Dispatch) => ({
  validate,
});

// Export connected component
// Passes validate function through map dispatch to props
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
