// @flow
import {
  SET_SELECTED_STRATEGY_ITEM,
  SET_STRATEGY_ITEM_EXPANDED,
  SET_ACTIVE_SCORECARD_MONTH,
} from '../actions/types';

import { type Action } from '../actions/types';
import { type ScorecardState } from './types';

const initialState: ScorecardState = {
  activeMonth: undefined,
  strategyMap: {
    selected: undefined,
    expanded: {
      STRATEGY_ROOT: true,
    },
  },
};

export default (state: ScorecardState = initialState, action: Action): ScorecardState => {
  switch (action.type) {
    case SET_STRATEGY_ITEM_EXPANDED:
      return {
        ...state,
        strategyMap: {
          ...state.strategyMap,
          expanded: {
            ...state.strategyMap.expanded,
            [action.id]: action.expanded,
          },
        },
      };
    case SET_SELECTED_STRATEGY_ITEM:
      return {
        ...state,
        strategyMap: {
          ...state.strategyMap,
          selected: action.element,
        },
      };
    case SET_ACTIVE_SCORECARD_MONTH:
      return {
        ...state,
        activeMonth: action.month,
      };
    default:
      return state;
  }
};
