// @flow
import moment from 'moment';

// Model Errors
import { InconsistentArgumentError } from '../../errors';

// API Functions
import { getScenarios } from '../Scenario';

// Constants
import { DATE_FORMAT } from '../../../../config/constants';

// Flow Types
import { type Model, type PhaseSensitivityData } from '../../types';

export function setPhaseSensitivityValues(
  model: Model,
  scenarioId: string,
  values: PhaseSensitivityData
): Model {
  // Retrieve existing scenarios
  const scenarios = getScenarios(model);

  // Map the existing scenarios to a new array
  const newScenarios = scenarios.map(scenario => {
    // If this scenario is the one to be updated
    if (scenario.id === scenarioId) {
      // Construct new phase sensitivity array
      const newPhaseSensitivity = values.map(value => {
        // Check date is valid
        const m = moment(value.start, DATE_FORMAT, true);
        if (!m.isValid()) {
          throw new InconsistentArgumentError(
            `Date ${value.start} was not in the required format '${DATE_FORMAT}'`
          );
        }

        // Return value as new sensitivity
        return value;
      });

      // Return the new scenario in same location
      return {
        ...scenario,
        phaseSensitivity: newPhaseSensitivity,
      };
    } else {
      // Return existing scenario
      return scenario;
    }
  });

  // Return model with scenarios updated
  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        scenarios: newScenarios,
      },
    ],
  };
}
