// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { updateObjectiveById } from '../Objective';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Perspective, PerspectiveData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of perspectives from the programme instance
 * @param {Model} model model instance
 * @return {Array<Perspective>} array of perspectives
 **/
export function getPerspectives(model: Model): Array<Perspective> {
  const programme = getProgramme(model);
  if (!programme.perspectives) {
    throw new InvalidModelError('Programme did not contain an array of perspectives');
  } else if (!Array.isArray(programme.perspectives)) {
    throw new InvalidModelError("Programme 'perspectives' was not an array");
  }
  return programme.perspectives;
}

/**
 * Returns a specific perspective from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id perspective id
 * @return {Perspective} perspective with provided id
 **/
export function getPerspectiveById(model: Model, id: string): Perspective {
  const perspectives = getPerspectives(model);
  const found = perspectives.find(perspective => perspective.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find perspective in programme with id ${id}`);
  }

  return found;
}

function checkPerspectiveData(perspective: PerspectiveData): boolean {
  if (perspective.hasOwnProperty('name')) {
    if (typeof perspective.name !== 'string' || perspective.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided perspective update value name was not a string (or it was empty)'
      );
    }
  }
  if (perspective.hasOwnProperty('weighting')) {
    if (
      typeof perspective.weighting !== 'number' ||
      Number.isNaN(perspective.weighting) ||
      perspective.weighting < 0
    ) {
      throw new InvalidUpdateError(
        'Provided perspective update value weighting was not a number (or it was negative)'
      );
    }
  }
  if (perspective.hasOwnProperty('definition')) {
    if (typeof perspective.definition !== 'string') {
      throw new InvalidUpdateError('Provided perspective update value definition was not a string');
    }
  }
  return true;
}

function checkTotalWeighting(perspectives: Array<Perspective>, newWeighting: number) {
  // Calculate current total weighting
  const currentSum = perspectives.reduce((acc, cur) => cur.weighting + acc, 0);

  // Throw error if new perspective will create invalid weights
  if (currentSum + newWeighting > 1) {
    throw new InvalidUpdateError(
      'New/changed perspective would yield a total weighting greater than 1'
    );
  }
}

/**
 * Returns a new model instance with a new perspective (with data if provided)
 * @param {Model} model model instance
 * @param {PerspectiveData} perspective optional perspective data to store as perspective
 * @return {Model} updated model instance
 */
export function newPerspective(model: Model, perspective?: PerspectiveData, id?: string): Model {
  // Default new perspective
  let perspectiveData = {
    id: id || nanoId(),
    name: `New Perspective ${++newCount}`,
    weighting: 0,
    definition: '',
  };

  if (perspective) {
    // Check validity of perspective data
    checkPerspectiveData(perspective);

    // Override default perspective data
    perspectiveData = { ...perspectiveData, ...perspective };

    // Check total weighting without new perspective
    checkTotalWeighting(model.programmes[0].perspectives, perspectiveData.weighting);
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        perspectives: [...model.programmes[0].perspectives, perspectiveData],
      },
    ],
  };
}

/**
 * Removes a perspective with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id perspective id
 * @returns {Model} updated model instance
 */
export function removePerspectiveById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'null'
): Model {
  if (!model.programmes[0].perspectives.find(p => p.id === id)) {
    throw new InconsistentArgumentError(
      `No perspective with id '${id}' was contained in the model instance`
    );
  }

  // Update objectives
  const { objectives } = getProgramme(model);
  let modifiedModel;
  switch (onDelete) {
    case 'null':
      modifiedModel = objectives
        .filter(o => o.perspectiveId === id)
        .reduce(
          (acc, cur) =>
            updateObjectiveById(acc, cur.id, {
              perspectiveId: 'none',
            }),
          model
        );
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when deleting objective');
  }

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        perspectives: modifiedModel.programmes[0].perspectives.filter(p => p.id !== id),
      },
    ],
  };
}

/**
 * Sets a perspective's attributes by id
 * @param {Model} model model instance
 * @param {string} id perspective id
 * @param {PerspectiveData} perspective new perspective
 * @return {Model} updated model instance
 */
export function updatePerspectiveById(
  model: Model,
  id: string,
  perspective: PerspectiveData
): Model {
  const found = model.programmes[0].perspectives.find(p => p.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No perspective with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkPerspectiveData(perspective);

  // Check total weights
  checkTotalWeighting(model.programmes[0].perspectives, perspective.weighting);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        perspectives: model.programmes[0].perspectives.map(p =>
          p.id === id
            ? {
                ...p,
                name: perspective.hasOwnProperty('name') ? perspective.name : p.name,
                weighting: perspective.hasOwnProperty('weighting')
                  ? perspective.weighting
                  : p.weighting,
                definition: perspective.hasOwnProperty('definition')
                  ? perspective.definition
                  : p.definition,
              }
            : p),
      },
    ],
  };
}
