function checkMatricesHaveCorrectPhaseKeys(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract benefits, deliverables, costs, phases, and matrices from model instance
  const {
    benefits,
    deliverables,
    costs,
    phases,
    benefitAttribution,
    benefitBuildup,
    costAttribution,
  } = model.programmes[0];

  // Benefit Attribution and Buildup
  benefits.forEach(benefit => {
    // Benefit Attribution

    deliverables.forEach(deliverable => {
      phases.forEach(phase => {
        if (!benefitAttribution[benefit.id]) return;
        if (!benefitAttribution[benefit.id][deliverable.id]) return;

        const datum = benefitAttribution[benefit.id][deliverable.id][phase.id];
        if (datum !== 0 && !datum) {
          valid.valid = false;

          const error = `${phase.name} does not have a key in benefit attribution`;
          if (!valid.errors.includes(error)) {
            valid.errors.push(error);
          }
        }
      });
    });

    // Benefit Buildup
    phases.forEach(phase => {
      if (!benefitBuildup[benefit.id]) return;

      const datum = benefitBuildup[benefit.id][phase.id];
      if (datum !== 0 && !datum) {
        valid.valid = false;

        const error = `${phase.name} does not have a key in benefit buildup`;
        if (!valid.errors.includes(error)) {
          valid.errors.push(error);
        }
      }
    });
  });

  // Cost Attribution
  costs.forEach(cost => {
    phases.forEach(phase => {
      if (!costAttribution[cost.id]) return;

      const datum = costAttribution[cost.id][phase.id];
      if (datum !== 0 && !datum) {
        valid.valid = false;

        const error = `${phase.name} does not have a key in cost attribution`;
        if (!valid.errors.includes(error)) {
          valid.errors.push(error);
        }
      }
    });
  });

  return valid;
}

function checkObjectivesReferenceValidPerspectives(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract perspectives and objectives from model instance
  const { perspectives, objectives } = model.programmes[0];

  // Check all objectives are sound
  objectives.forEach(objective => {
    if (objective.perspectiveId === 'none') return;

    const found = perspectives.find(perspective => perspective.id === objective.perspectiveId);

    if (!found) {
      valid.valid = false;
      valid.errors.push(`${objective.name} referenced a perspective which did not exist`);
    }
  });

  return valid;
}

