function checkPhaseLinksReferenceValidPhases(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract objectives and measures from model instance
  const { phases, phaseLinks } = model.programmes[0];

  // Check all measures are sound
  phaseLinks.forEach(phaseLink => {
    if (phaseLink.sourceId === phaseLink.targetId) {
      valid.valid = false;
      valid.errors.push(`Phase link (${phaseLink.id}) is a circular reference`);
    }

    // Find referenced phases
    const foundTarget = phases.find(phase => phase.id === phaseLink.targetId);
    const foundSource = phases.find(phase => phase.id === phaseLink.targetId);

    // Check target
    if (!foundTarget) {
      valid.valid = false;
      valid.errors.push(`Phase link (${phaseLink.id}) target phase does not exist`);
    }

    // Check source
    if (!foundSource) {
      valid.valid = false;
      valid.errors.push(`Phase link (${phaseLink.id}) source phase does not exist`);
    }
  });

  return valid;
}

function checkPhaseSensitivitiesReferenceValidPhases(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract phases and scenarios from model instance
  const { phases, scenarios } = model.programmes[0];

  // Check all scenarios are sound
  scenarios.forEach(scenario => {
    const { phaseSensitivity } = scenario;

    phaseSensitivity.forEach(phaseSensitivity => {
      // Find referenced phase
      const found = phases.find(phase => phase.id === phaseSensitivity.id);

      // Check phase exists
      if (!found) {
        valid.valid = false;
        valid.errors.push(
          `Phase sensitivity in ${scenario.name} references phase which does not exist`
        );
      }
    });
  });

  return valid;
}

