function checkPhaseCostSensitivitiesContainPhaseKeys(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract phases and scenarios from model instance
  const { phases, scenarios } = model.programmes[0];

  // Store cost types
  const costTypes = [COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT];

  // Check scenarios are sound
  scenarios.forEach(scenario => {
    costTypes.forEach(costType => {
      phases.forEach(phase => {
        const { phaseCostSensitivity } = scenario;

        const datum = phaseCostSensitivity[costType][phase.id];
        if (datum !== 0 && !datum) {
          valid.valid = false;

          const error = `${phase.name} does not have a key in the ${
            scenario.name
          } phase cost sensitivity`;
          if (!valid.errors.includes(error)) {
            valid.errors.push(error);
          }
        }
      });
    });
  });

  return valid;
}

