function checkThreadsReferenceValidElements(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract threads and elements from model instance
  const {
    benefits,
    deliverables,
    stakeholders,
    measures,
    groups,
    beneficiaries,
    causeAndEffect,
  } = model.programmes[0];

  causeAndEffect.forEach(thread => {
    // Check benefit exists
    const foundBenefit = benefits.find(benefit => benefit.id === thread.benefitId);
    if (!foundBenefit) {
      valid.valid = false;
      valid.errors.push(
        `thread ${thread.reference} references benefit (${thread.benefitId}) which does not exist`
      );
    }

    // Check deliverable exists
    const foundDeliverable = deliverables.find(
      deliverable => deliverable.id === thread.deliverableId
    );
    if (!foundDeliverable) {
      valid.valid = false;
      valid.errors.push(
        `thread ${thread.reference} references deliverable (${
          thread.deliverableId
        }) which does not exist`
      );
    }

    // Check stakeholder exists
    const foundStakeholder = stakeholders.find(
      stakeholder => stakeholder.id === thread.stakeholderId
    );
    if (!foundStakeholder) {
      valid.valid = false;
      valid.errors.push(
        `thread ${thread.reference} references stakeholder (${
          thread.stakeholderId
        }) which does not exist`
      );
    }

    // Check measure exists
    if (thread.measureId !== 'none') {
      const foundMeasure = measures.find(measure => measure.id === thread.measureId);
      if (!foundMeasure) {
        valid.valid = false;
        valid.errors.push(
          `thread ${thread.reference} references measure (${thread.measureId}) which does not exist`
        );
      }
    }

    // Check group exists
    if (thread.groupId !== 'none') {
      const foundGroup = groups.find(group => group.id === thread.groupId);
      if (!foundGroup) {
        valid.valid = false;
        valid.errors.push(
          `thread ${thread.reference} references group (${thread.groupId}) which does not exist`
        );
      }
    }

    // Check beneficiary exists
    if (thread.beneficiaryId !== 'none') {
      const foundBeneficiary = beneficiaries.find(
        beneficiary => beneficiary.id === thread.beneficiaryId
      );
      if (!foundBeneficiary) {
        valid.valid = false;
        valid.errors.push(
          `thread ${thread.reference} references beneficiary (${
            thread.beneficiaryId
          }) which does not exist`
        );
      }
    }
  });

  return valid;
}

function checkMeasureData(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract measures and scorecard start from model instance
  const { measures, scorecardStart: start } = model.programmes[0];

  measures.forEach(measure => {
    // Create moment from scorecard start
    const scorecardStart = moment(start, DATE_FORMAT, true);

    // Check baseline start occurs on or after scorecard start
    const baselineStart = moment(measure.baselineStart, DATE_FORMAT, true);
    if (baselineStart.isBefore(scorecardStart, 'days')) {
      valid.valid = false;
      valid.errors.push(`${measure.name} baseline start occurs before scorecard start`);
    }

    // Check baseline complete occurs after baseline start
    const baselineComplete = moment(measure.baselineComplete, DATE_FORMAT, true);
    if (baselineComplete.isSameOrBefore(baselineStart, 'days')) {
      valid.valid = false;
      valid.errors.push(`${measure.name} baseline complete occurs on or before baseline start`);
    }

    // Determine upper, lower bound and direction of travel
    let upper, lower, direction;

    // Compare best and worst values
    if (measure.best === measure.worst && measure.best !== 0) {
      valid.valid = false;
      valid.errors.push(`${measure.name} best and worst values were the same`);
      return;
    } else if (measure.best > measure.worst) {
      upper = measure.best;
      lower = measure.worst;
      direction = 'INCREASE';
    } else if (measure.best < measure.worst) {
      upper = measure.worst;
      lower = measure.best;
      direction = 'DECREASE';
    } else {
      upper = measure.best;
      lower = measure.worst;
      direction = 'ZERO';
    }

    // Check values exist within boundaries
    const { baselineValue, standard, minimumStandard, change } = measure;

    // Check baseline value
    if (direction !== 'ZERO') {
      if (baselineValue > upper) {
        valid.valid = false;
        valid.errors.push(`${measure.name} baseline value was greater than the upper bound`);
      } else if (baselineValue < lower) {
        valid.valid = false;
        valid.errors.push(`${measure.name} baseline value was smaller than the lower bound`);
      }

      // Check standard
      if (standard > upper) {
        valid.valid = false;
        valid.errors.push(`${measure.name} standard value was greater than the upper bound`);
      } else if (standard < lower) {
        valid.valid = false;
        valid.errors.push(`${measure.name} standard value was smaller than the lower bound`);
      }

      // Check minimum standard
      if (minimumStandard > upper) {
        valid.valid = false;
        valid.errors.push(
          `${measure.name} minimum standard value was greater than the upper bound`
        );
      } else if (minimumStandard < lower) {
        valid.valid = false;
        valid.errors.push(
          `${measure.name} minimum standard value was smaller than the lower bound`
        );
      }
    }

    // Check standard, minimum standard and change respect best and worst direction
    if (direction === 'INCREASE') {
      // Check change value
      if (change < 0) {
        valid.valid = false;
        valid.errors.push(`${measure.name}'s best was greater than worst but change was negative`);
      }

      // Check minimum standard is lower than standard
      if (minimumStandard > standard) {
        valid.valid = false;
        valid.errors.push(`${measure.name} minimum standard was above standard`);
      }
    } else if (direction === 'DECREASE') {
      // Check change value
      if (change > 0) {
        valid.valid = false;
        valid.errors.push(`${measure.name}'s best was smaller than worst but change was positive`);
      }

      // Check minimum standard is greater than standard
      if (minimumStandard < standard) {
        valid.valid = false;
        valid.errors.push(`${measure.name} minimum standard was below standard`);
      }
    }
  });

  return valid;
}

function checkStrategyMapWeightings(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract measures, objectives, and perspectives from model instance
  const { measures, objectives, perspectives } = model.programmes[0];

  // Check perspectives sum to 1
  const perspectiveSum = perspectives.reduce((sum, perspective) => sum + perspective.weighting, 0);
  if (perspectiveSum > 1) {
    valid.valid = false;
    valid.errors.push('total perspective weighting was greater than 1');
  }

  // Check objectives under same perspective are not greater 1
  perspectives.forEach(perspective => {
    const objectiveSum = objectives
      .filter(objective => objective.perspectiveId === perspective.id)
      .reduce((sum, objective) => sum + objective.weighting, 0);

    if (objectiveSum > 1) {
      valid.valid = false;
      valid.errors.push(`total objective weighting for ${perspective.name} was greater than 1`);
    }
  });

  // Check measures under same objective are not greater 1
  objectives.forEach(objective => {
    const measureSum = measures
      .filter(measure => measure.objectiveId === objective.id)
      .reduce((sum, measure) => sum + measure.weighting, 0);

    if (measureSum > 1) {
      valid.valid = false;
      valid.errors.push(`total measure weighting for ${objective.name} was greater than 1`);
    }
  });

  return valid;
}

function checkPhaseDates(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract number of years of analysis from model
  const { _analysisYears: analysisYears } = model;

  // Extract phases and programme start date from model instance
  const { phases, start } = model.programmes[0];

  // Create moment from programme start
  const programmeStart = moment(start, DATE_FORMAT, true);
  const analysisEnds = moment(programmeStart).add(analysisYears, 'years');

  phases.forEach(phase => {
    // Create moment when phase starts
    const phaseStart = moment(phase.start, DATE_FORMAT, true);

    // Check phase start is after programme start
    if (programmeStart.isAfter(phaseStart)) {
      valid.valid = false;
      valid.errors.push(`${phase.name} starts after programme start`);
    }

    // Create moment when phase ends
    const phaseEnds = moment(phaseStart).add(phase.duration, 'weeks');

    // Check phase ends before analysis ends
    if (phaseEnds.isAfter(analysisEnds)) {
      valid.valid = false;
      valid.errors.push(`${phase.name} finishes after analysis period ends`);
    }
  });

  return valid;
}

function checkAllBenefitAttributionKeysExist(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract benefit attribution, benefits, deliverables, and phases from model
  const { benefitAttribution, benefits, deliverables, phases } = model.programmes[0];

  // Map element arrays to ids
  const benefitIds = benefits.map(benefit => benefit.id);
  const deliverableIds = deliverables.map(deliverable => deliverable.id);
  const phaseIds = phases.map(phase => phase.id);

  // Extract benefit attribution keys
  const benefitKeys = Object.keys(benefitAttribution);

  benefitKeys.forEach((benefitKey: string) => {
    // Check key is valid
    if (!benefitIds.includes(benefitKey)) {
      valid.valid = false;

      const benefitError = 'benefit attribution contained benefit key which does not exist';
      if (!valid.errors.includes(benefitError)) {
        valid.errors.push(benefitError);
      }
    }

    // If the key exists in benefit attribution
    if (benefitAttribution[benefitKey]) {
      // Extract benefit attribution deliverable keys
      const deliverableKeys = Object.keys(benefitAttribution[benefitKey]);

      deliverableKeys.forEach((deliverableKey: string) => {
        // Check key is valid
        if (!deliverableIds.includes(deliverableKey)) {
          valid.valid = false;

          const deliverableError =
            'benefit attribution contained deliverable key which does not exist';
          if (!valid.errors.includes(deliverableError)) {
            valid.errors.push(deliverableError);
          }
        }

        // If the keys exists in benefit attribution
        if (benefitAttribution[benefitKey][deliverableKey]) {
          // Extract phase keys
          const phaseKeys = Object.keys(benefitAttribution[benefitKey][deliverableKey]);

          phaseKeys.forEach((phaseKey: string) => {
            // Check key is valid
            if (!phaseIds.includes(phaseKey)) {
              valid.valid = false;

              const phaseError = 'benefit attribution contained phase key which does not exist';
              if (!valid.errors.includes(phaseError)) {
                valid.errors.push(phaseError);
              }
            }
          });
        }
      });
    }
  });

  return valid;
}

