function checkAllThreadsHaveThreadValues(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract scenarios, and phases from model
  const { _threadValues: values, causeAndEffect } = model.programmes[0];

  // Determine present value ids
  const valueIds = values.map(value => value.id);

  // Check all thread value entries
  causeAndEffect.forEach(thread => {
    if (!valueIds.includes(thread.id)) {
      valid.valid = false;
      valid.errors.push(`thread ${thread.reference} did not have associated thread values`);
    }
  });

  if (hasDuplicates(valueIds)) {
    valid.valid = false;
    valid.errors.push('thread values contain duplicate thread ids');
  }

  return valid;
}

function checkAllThreadValuesAreValid(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract analysis years from model
  const { _analysisYears: years } = model;

  // Extract scenarios, and phases from model
  const { _threadValues: values, causeAndEffect } = model.programmes[0];

  // Map all thread ids from cause and effect
  const threadIds = causeAndEffect.map(thread => thread.id);

  // Check all thread value entries
  values.forEach(value => {
    if (!threadIds.includes(value.id)) {
      valid.valid = false;
      valid.errors.push('thread value referenced cause and effect thread which does not exist');
      return;
    }

    // Get thread reference for better error message
    const foundThread = causeAndEffect.find(thread => thread.id === value.id);
    const threadReference = (foundThread && foundThread.reference) || value.id;

    // Check if there are too many values
    if (value.values.length > years) {
      valid.valid = false;
      valid.errors.push(
        `thread values for ${threadReference} had too many values for analysis years`
      );
    } else if (value.values.length < years) {
      valid.valid = false;
      valid.errors.push(
        `thread values for ${threadReference} did not have enough values for analysis years`
      );
    }
  });

  return valid;
}

function checkNoDuplicateSensitivitiesExist(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract scenarios and benefits from model
  const { scenarios } = model.programmes[0];

  // Check all scenarios are sound
  scenarios.forEach(scenario => {
    // Extract sensitivities
    const { benefitSensitivity, deliverableSensitivity, phaseSensitivity } = scenario;

    // Map to ids
    const benefitIds = benefitSensitivity.map(sensitivity => sensitivity.id);
    const deliverableIds = deliverableSensitivity.map(sensitivity => sensitivity.id);
    const phaseIds = phaseSensitivity.map(sensitivity => sensitivity.id);

    if (hasDuplicates(benefitIds)) {
      valid.valid = false;
      valid.errors.push(`duplicate benefit ids exist in ${scenario.name} sensitivity`);
    }

    if (hasDuplicates(deliverableIds)) {
      valid.valid = false;
      valid.errors.push(`duplicate deliverable ids exist in ${scenario.name} sensitivity`);
    }

    if (hasDuplicates(phaseIds)) {
      valid.valid = false;
      valid.errors.push(`duplicate phase ids exist in ${scenario.name} sensitivity`);
    }
  });

  return valid;
}

function checkBenefitAttributionSumsToOne(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract benefit attribution, benefits, deliverable, and phases from model
  const { benefitAttribution, benefits, deliverables, phases } = model.programmes[0];

  benefits.forEach(benefit => {
    if (benefitAttribution[benefit.id]) {
      const benefitMatrix = benefitAttribution[benefit.id];

      deliverables.forEach(deliverable => {
        if (benefitMatrix[deliverable.id]) {
          const deliverableMatrix = benefitMatrix[deliverable.id];

          const sum = phases.reduce((sum, phase) => {
            if (deliverableMatrix[phase.id]) {
              return sum + deliverableMatrix[phase.id];
            }
            return sum;
          }, 0);

          if (sum && sum !== 1) {
            valid.valid = false;
            valid.errors.push(
              `benefit attribution for ${deliverable.name} in ${
                benefit.name
              } does not sum to 1 or 0`
            );
          }
        }
      });
    }
  });

  return valid;
}

function checkBalancedScorecardContainsCorrectInformation(model: Model): ValidObject {
  // Define new validity object
  const valid = { valid: true, errors: [] };

  // Extract analysis years, balanced scorecard, measures and scorecard start from model
  const { _analysisYears: analysisYears } = model;
  const { balancedScorecard, measures, scorecardStart } = model.programmes[0];

  // Map measure ids
  const measureIds = measures.map(measure => measure.id);

  // Define expected months
  const months = [];
  const start = moment(scorecardStart, DATE_FORMAT, true);
  for (let m = 0; m < 12 * analysisYears; m++) {
    months.push(
      moment(start)
        .add(m, 'months')
        .format(DATE_FORMAT)
    );
  }

  balancedScorecard.forEach(monthItem => {
    if (!months.includes(monthItem.date)) {
      valid.valid = false;
      valid.errors.push(`balanced scorecard contained invalid date ${monthItem.date}`);
    }

    const { values } = monthItem;

    values.forEach(measureEntry => {
      if (!measureIds.includes(measureEntry.id)) {
        valid.valid = false;

        const error = `balanced scorecard contained non-existent measure ${measureEntry.id}`;
        if (!valid.errors.includes(error)) {
          valid.errors.push(error);
        }
      }

      const targetMeasure = measures.find(measure => measure.id === measureEntry.id);

      if (targetMeasure) {
        // Extract best and worst values
        const { best, worst } = targetMeasure;

        if (best === worst && best === 0 && measureEntry.value !== 0) {
          valid.valid = false;
          valid.errors.push(
            'balanced scorecard measure entry was not zero despite best and worst being zero'
          );
        } else if (
          best > worst && 
          measureEntry.value !== 0 && 
          (measureEntry.value > best || measureEntry.value < worst)
        ) {
          valid.valid = false;
          valid.errors.push('balanced scorecard measure entry was not in range [worst, best]');
        } else if (
          best < worst && 
          measureEntry.value !== 0 && 
          (measureEntry.value > worst || measureEntry.value < best)
        ) {
          valid.valid = false;
          valid.errors.push('balanced scorecard measure entry was not in range [best, worst]');
        }
      }
    });
  });

  return valid;
}

function mergeValidityObjects(objects: Array<ValidObject>): ValidObject {
  return objects.reduce(
    (merged, obj) => {
      merged.valid = obj.valid && merged.valid;

      if (!obj.valid) {
        obj.errors.forEach(error => merged.errors.push(error));
      }

      return merged;
    },
    { valid: true, errors: [] }
  );
}

function isConsistent(model: Model): ValidObject {
  const validObjects = [];

  // Model
  validObjects.push(checkForDuplicateIds(model));

  // Programme
  validObjects.push(checkProgrammeDates(model));
  validObjects.push(checkElementsHaveUniqueNames(model));

  // Benefits
  validObjects.push(checkCoreBenefits(model));
  validObjects.push(checkMatricesHaveCorrectBenefitKeys(model));
  validObjects.push(checkBenefitSensitivitiesReferenceValidBenefits(model));
  validObjects.push(checkBenefitBuildupSensitivitiesContainBenefitKeys(model));

  // Deliverables
  validObjects.push(checkMatricesHaveCorrectDeliverableKeys(model));
  validObjects.push(checkDeliverableSensitivitiesReferenceValidDeliverables(model));

  // Phases
  validObjects.push(checkPhaseDates(model));
  validObjects.push(checkMatricesHaveCorrectPhaseKeys(model));
  validObjects.push(checkPhaseLinksReferenceValidPhases(model));
  validObjects.push(checkPhaseSensitivitiesReferenceValidPhases(model));
  validObjects.push(checkPhaseCostSensitivitiesContainPhaseKeys(model));
  validObjects.push(checkBenefitBuildupSensitivitiesContainPhaseKeys(model));

  // Costs
  validObjects.push(checkMatricesHaveCorrectCostKeys(model));

  // Stakeholders
  validObjects.push(checkMatricesHaveCorrectStakeholderKeys(model));

  // Measures
  validObjects.push(checkMeasureData(model));

  // Cause and effect
  validObjects.push(checkThreadsReferenceValidElements(model));
  validObjects.push(checkAllThreadsHaveThreadValues(model));
  validObjects.push(checkAllThreadsHaveUniqueReferences(model));

  // Thread Values
  validObjects.push(checkAllThreadValuesAreValid(model));

  // Benefit Attribution
  validObjects.push(checkAllBenefitAttributionKeysExist(model));
  validObjects.push(checkBenefitAttributionSumsToOne(model));

  // Benefit Buildup
  validObjects.push(checkAllBenefitBuildupKeysExist(model));

  // Stakeholder Benefits
  validObjects.push(checkAllStakeholderBenefitKeysExist(model));

  // Cost Attribution
  validObjects.push(checkAllCostAttributionKeysExist(model));

  // Strategy Map
  validObjects.push(checkObjectivesReferenceValidPerspectives(model));
  validObjects.push(checkMeasuresReferenceValidObjectives(model));
  validObjects.push(checkStrategyMapWeightings(model));

  // Scenarios
  validObjects.push(checkNoDuplicateSensitivitiesExist(model));
  validObjects.push(checkBaselineScenario(model));
  validObjects.push(checkPhaseSensitivitiesContainAllPhases(model));
  validObjects.push(checkBenefitSensitivitiesContainAllBenefits(model));
  validObjects.push(checkDeliverableSensitivitiesContainAllDeliverables(model));
  validObjects.push(checkAllBenefitBuildupSensitivityKeysExist(model));
  validObjects.push(checkAllPhaseCostSensitivityKeysExist(model));

  // Balanced Scorecard
  validObjects.push(checkBalancedScorecardContainsCorrectInformation(model));

  return mergeValidityObjects(validObjects);
}

export default isConsistent;
