// @flow
import { InvalidModelError, InconsistentArgumentError, InvalidUpdateError } from '../../errors';
import { getProgramme } from '../Programme';

// Flow Types
import type { Model } from '../../types';

/**
 * Returns the element at the provided location in the matrix (referenced by
 * 'development'/'support' and id)
 * @param {Model} model model instance
 * @param {Array<string>} location array of string ids indicating location within matrix
 * @return {number} entry at location
 **/
export function getCostAttributionEntry(model: Model, location: Array<string>): number {
  const programme = getProgramme(model);
  if (!programme.costAttribution) {
    throw new InvalidModelError("The programme did not contain the 'costAttribution' attribute");
  } else if (!programme.costAttribution[location[0]]) {
    throw new InconsistentArgumentError(
      `The programme 'costAttribution' did not contain a key for ${location[0]}`
    );
  } else if (!programme.costAttribution[location[0]][location[1]]) {
    throw new InconsistentArgumentError(
      `The programme 'costAttribution' did not contain the secondary key ${location[1]}`
    );
  } else if (typeof programme.costAttribution[location[0]][location[1]] === 'number') {
    throw new InvalidModelError(`Entry at location ${location[0]}-${location[1]} was not a number`);
  } else if (Number.isNaN(programme.costAttribution[location[0]][location[1]])) {
    throw new InvalidModelError(
      `Entry at location ${location[0]}-${location[1]} was NaN (not a number)`
    );
  }

  return programme.costAttribution[location[0]][location[1]];
}

/**
 * Update the cost attribution in the model with a type-checked set of new values
 * @param {Model} model model instance
 * @param {Matrix2D} values values to update with
 * @return {Model} updated model instance
 */
export function setCostAttribution(
  model: Model,
  values: { [string]: { [string]: number } }
): Model {
  // Throw error if values is undefined
  if (!values) {
    throw new InvalidUpdateError('Tried to update cost attribution with undefined values');
  }

  // Get programme instance
  const programme = getProgramme(model);

  // Throw error if costAttribution not defined
  if (!programme.costAttribution) {
    throw new InvalidModelError("The programme did not contain the 'costAttribution' attribute");
  }

  // Extract cost ids from provided values
  const costIds = Object.keys(values);
  const costIdsValid = costIds.reduce(
    (acc, id) => acc && programme.costs.map(c => c.id).includes(id),
    true
  );
  if (!costIdsValid || costIds.length !== programme.costs.length) {
    throw new InvalidUpdateError('The cost keys in the provided update values were invalid');
  }

  // Extract phase ids from provided values
  const phaseIds = Object.keys(values[costIds[0]]);
  const phaseIdsValid = phaseIds.reduce(
    (acc, id) => acc && programme.phases.map(p => p.id).includes(id),
    true
  );
  if (!phaseIdsValid || phaseIds.length !== programme.phases.length) {
    throw new InvalidUpdateError('The phase keys in the provided update values were invalid');
  }

  // Create a new object which we will insert type checked values
  const newMatrix = {};

  // Loop each cost id...
  costIds.forEach(cId => {
    // Declare an object to store cost data in
    const newCostObject = {};

    phaseIds.forEach(pId => {
      // If value is properly defined and a number update matrix
      if (typeof values[cId][pId] === 'number' && !Number.isNaN(values[cId][pId])) {
        // Set cost object phase key to values
        newCostObject[pId] = values[cId][pId];
      } else if (!values[cId][pId]) {
        // Throw error if deliverable object not defined for this cost
        throw new InvalidUpdateError(
          'Tried to update cost attribution when values did not have the correct phase keys'
        );
      } else {
        // Throw error if value was not a number (NaN or different type)
        throw new InvalidUpdateError(
          'Tried to update cost attribution when some values were not a number'
        );
      }
    });

    // Update the matrix object with the cost object
    newMatrix[cId] = newCostObject;
  });

  // Finally, return new model instance with benefit attribution swapped out
  return {
    ...model,
    programmes: [
      {
        ...programme,
        costAttribution: newMatrix,
      },
    ],
  };
}
