// @flow
import { SET_SELECTED_CERTAINTY_ELEMENT, SET_CERTAINTY_ELEMENT_EXPANDED } from '../actions/types';
import type { Action } from '../actions/types';
import type { CertaintyState } from './types';

const initialState: CertaintyState = {
  explorer: {
    selected: undefined,
    expanded: {
      BASELINE_SCENARIO: true,
    },
  },
  dark: false,
};

export default (state: CertaintyState = initialState, action: Action) => {
  switch (action.type) {
    case SET_CERTAINTY_ELEMENT_EXPANDED:
      return {
        ...state,
        explorer: {
          ...state.explorer,
          expanded: {
            ...state.explorer.expanded,
            [action.id]: action.expanded,
          },
        },
      };
    case SET_SELECTED_CERTAINTY_ELEMENT:
      return {
        ...state,
        explorer: {
          ...state.explorer,
          selected: action.element,
        },
      };
    default:
      return state;
  }
};
