// @flow
import nanoId from 'nanoid';

import { getProgramme } from '../Programme';
import { getPerspectives } from '../Perspective';
import { updateMeasureById } from '../Measure';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Objective, ObjectiveData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of objectives from the programme instance
 * @param {Model} model model instance
 * @return {Array<Objective>} array of objectives
 **/
export function getObjectives(model: Model): Array<Objective> {
  const programme = getProgramme(model);
  if (!programme.objectives) {
    throw new InvalidModelError('Programme did not contain an array of objectives');
  } else if (!Array.isArray(programme.objectives)) {
    throw new InvalidModelError("Programme 'objectives' was not an array");
  }
  return programme.objectives;
}

/**
 * Returns a specific objective from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id objective id
 * @return {Objective} objective with provided id
 **/
export function getObjectiveById(model: Model, id: string): Objective {
  const objectives = getObjectives(model);
  const found = objectives.find(objective => objective.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find objective in programme with id ${id}`);
  }

  return found;
}

function checkObjectiveData(objective: ObjectiveData, model: Model): boolean {
  if (objective.hasOwnProperty('name')) {
    if (typeof objective.name !== 'string' || objective.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided objective update value name was not a string (or it was empty)'
      );
    }
  }
  if (objective.hasOwnProperty('perspectiveId')) {
    if (typeof objective.perspectiveId !== 'string' || objective.perspectiveId.length === 0) {
      throw new InvalidUpdateError(
        'Provided objective update value perspectiveId was not a string (or it was empty)'
      );
    }

    if (objective.perspectiveId !== 'none') {
      const found = getPerspectives(model).find(
        perspective => perspective.id === objective.perspectiveId
      );

      if (!found) {
        throw new InvalidUpdateError(
          'Provided objective perspective id referenced non-existent perspective'
        );
      }
    }
  }
  if (objective.hasOwnProperty('weighting')) {
    if (typeof objective.weighting !== 'number' || Number.isNaN(objective.weighting)) {
      throw new InvalidUpdateError('Provided objective update value weighting was not a number');
    }
  }
  if (objective.hasOwnProperty('owner')) {
    if (typeof objective.owner !== 'string') {
      throw new InvalidUpdateError('Provided objective update value owner was not a string');
    }
  }
  if (objective.hasOwnProperty('definition')) {
    if (typeof objective.definition !== 'string') {
      throw new InvalidUpdateError('Provided objective update value definition was not a string');
    }
  }
  return true;
}

function checkTotalWeighting(model: Model, perspectiveId: string, weighting: number) {
  const applicableObjectives = model.programmes[0].objectives.filter(
    objective => objective.perspectiveId === perspectiveId
  );
  const currentTotalWeighting = applicableObjectives.reduce((acc, cur) => cur.weighting + acc, 0);

  if (currentTotalWeighting + weighting > 1) {
    throw new InvalidUpdateError(
      'New/changed measure would yield a total weighting for objective greater than 1'
    );
  }
}
/**
 * Returns a new model instance with a new objective (with data if provided)
 * @param {Model} model model instance
 * @param {ObjectiveData} objective optional objective data to store as objective
 * @return {Model} updated model instance
 */
export function newObjective(model: Model, objective?: ObjectiveData, id?: string): Model {
  // Default new objective
  let objectiveData = {
    id: id || nanoId(),
    name: `New Objective ${++newCount}`,
    perspectiveId: getPerspectives(model)[0].id,
    weighting: 0,
    owner: '',
    definition: '',
  };

  if (objective) {
    // Check validity of objective data
    checkObjectiveData(objective, model);

    // Override default objective data
    objectiveData = { ...objectiveData, ...objective };

    checkTotalWeighting(model, objectiveData.perspectiveId, objectiveData.weighting);
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        objectives: [...model.programmes[0].objectives, objectiveData],
      },
    ],
  };
}

/**
 * Removes a objective with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id objective id
 * @returns {Model} updated model instance
 */
export function removeObjectiveById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'null'
): Model {
  if (!model.programmes[0].objectives.find(o => o.id === id)) {
    throw new InconsistentArgumentError(
      `No objective with id '${id}' was contained in the model instance`
    );
  }

  // Update measures
  const { measures } = getProgramme(model);
  let modifiedModel;
  switch (onDelete) {
    case 'null':
      modifiedModel = measures
        .filter(m => m.objectiveId === id)
        .reduce((acc, cur) => updateMeasureById(acc, cur.id, { objectiveId: 'none' }), model);
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when deleting objective');
  }

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        objectives: modifiedModel.programmes[0].objectives.filter(o => o.id !== id),
      },
    ],
  };
}

/**
 * Sets a objective's attributes by id
 * @param {Model} model model instance
 * @param {string} id objective id
 * @param {ObjectiveData} objective new objective
 * @return {Model} updated model instance
 */
export function updateObjectiveById(model: Model, id: string, objective: ObjectiveData): Model {
  // Retrieve existing objective to update
  const existingObjective = getObjectiveById(model, id);

  // Check validity of data
  checkObjectiveData(objective, model);

  // Determine perspective id and weighting
  const perspectiveId = objective.perspectiveId || existingObjective.perspectiveId;
  const weighting =
    objective.weighting === 0 ? 0 : objective.weighting || existingObjective.weighting;

  // Confirm total weighting is within bounds
  checkTotalWeighting(model, perspectiveId, weighting);

  // Update measure
  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        objectives: model.programmes[0].objectives.map(o =>
          o.id === id
            ? {
                ...o,
                name: objective.hasOwnProperty('name') ? objective.name : o.name,
                perspectiveId: objective.hasOwnProperty('perspectiveId')
                  ? objective.perspectiveId
                  : o.perspectiveId,
                weighting: objective.hasOwnProperty('weighting')
                  ? objective.weighting
                  : o.weighting,
                owner: objective.hasOwnProperty('owner') ? objective.owner : o.owner,
                definition: objective.hasOwnProperty('definition')
                  ? objective.definition
                  : o.definition,
              }
            : o),
      },
    ],
  };
}
