// @flow
// ID Generator
import nanoId from 'nanoid';

// Date Time Library
import moment from 'moment';

// API Functions
import { getProgramme, getProgrammeStart, getProgrammeUnitOfMeasure } from '../Programme';
import { updateThreadById, getThreadById } from '../Thread';
import { getObjectives } from '../Objective';
import { getPhases } from '../Phase';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Constants
import { DATE_FORMAT } from '../../../../config/constants';

// Flow Types
import type { Measure, MeasureData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of measure from the programme instance
 * @param {Model} model model instance
 * @return {Array<Measure>} array of measure
 **/
export function getMeasures(model: Model): Array<Measure> {
  const programme = getProgramme(model);
  if (!programme.measures) {
    throw new InvalidModelError('Programme did not contain an array of measure');
  } else if (!Array.isArray(programme.measures)) {
    throw new InvalidModelError("Programme 'measure' was not an array");
  }
  return programme.measures;
}

/**
 * Returns a specific measure from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id measure id
 * @return {Measure} measure with provided id
 **/
export function getMeasureById(model: Model, id: string): Measure {
  const measures = getMeasures(model);
  const found = measures.find(measure => measure.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find measure in programme with id ${id}`);
  }

  return found;
}

function checkMeasureData(measure: MeasureData): boolean {
  if (measure.hasOwnProperty('name')) {
    if (typeof measure.name !== 'string' || measure.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided measure update value name was not a string (or it was empty)'
      );
    }
  }
  if (measure.hasOwnProperty('objectiveId')) {
    if (typeof measure.objectiveId !== 'string' || measure.objectiveId.length === 0) {
      throw new InvalidUpdateError(
        'Provided measure update value objectiveId was not a string (or it was empty)'
      );
    }
  }
  if (measure.hasOwnProperty('weighting')) {
    if (
      typeof measure.weighting !== 'number' ||
      Number.isNaN(measure.weighting) ||
      measure.weighting < 0
    ) {
      throw new InvalidUpdateError(
        'Provided measure update value weighting was not a number (or was negative)'
      );
    } else if (measure.weighting > 1) {
      throw new InvalidUpdateError('Provided measure update value weighting was greater than 1');
    }
  }
  if (measure.hasOwnProperty('dataOwner')) {
    if (typeof measure.dataOwner !== 'string') {
      throw new InvalidUpdateError('Provided measure update value dataOwner was not a string');
    }
  }
  if (measure.hasOwnProperty('definition')) {
    if (typeof measure.definition !== 'string') {
      throw new InvalidUpdateError('Provided measure update value definition was not a string');
    }
  }
  if (measure.hasOwnProperty('unitOfMeasure')) {
    if (typeof measure.unitOfMeasure !== 'string') {
      throw new InvalidUpdateError('Provided measure update value unitOfMeasure was not a string');
    }
  }
  if (measure.hasOwnProperty('baselineStart')) {
    if (
      typeof measure.baselineStart !== 'string' ||
      !moment(measure.baselineStart, DATE_FORMAT, true).isValid()
    ) {
      throw new InvalidUpdateError(
        'Provided measure update value baselineStart was not a string or it was not in valid date format'
      );
    }
  }
  if (measure.hasOwnProperty('baselineComplete')) {
    if (
      typeof measure.baselineComplete !== 'string' ||
      !moment(measure.baselineComplete, DATE_FORMAT, true).isValid()
    ) {
      throw new InvalidUpdateError(
        'Provided measure update value baselineComplete was not a string or it was not in valid date format'
      );
    }
  }
  if (measure.hasOwnProperty('baselineValue')) {
    if (typeof measure.baselineValue !== 'number' || Number.isNaN(measure.baselineValue)) {
      throw new InvalidUpdateError('Provided measure update value baselineValue was not a number');
    }
  }
  if (measure.hasOwnProperty('change')) {
    if (typeof measure.change !== 'number' || Number.isNaN(measure.change)) {
      throw new InvalidUpdateError('Provided measure update value change was not a number');
    }
  }
  if (measure.hasOwnProperty('best')) {
    if (typeof measure.best !== 'number' || Number.isNaN(measure.best)) {
      throw new InvalidUpdateError('Provided measure update value best was not a number');
    }
  }
  if (measure.hasOwnProperty('worst')) {
    if (typeof measure.worst !== 'number' || Number.isNaN(measure.worst)) {
      throw new InvalidUpdateError('Provided measure update value worst was not a number');
    }
  }
  if (measure.hasOwnProperty('standard')) {
    if (typeof measure.standard !== 'number' || Number.isNaN(measure.standard)) {
      throw new InvalidUpdateError('Provided measure update value standard was not a number');
    }
  }
  if (measure.hasOwnProperty('minimumStandard')) {
    if (typeof measure.minimumStandard !== 'number' || Number.isNaN(measure.minimumStandard)) {
      throw new InvalidUpdateError(
        'Provided measure update value minimumStandard was not a number'
      );
    }
  }
  return true;
}

function checkTotalWeighting(model: Model, objectiveId: string, weighting: number) {
  const applicableMeasures = model.programmes[0].measures.filter(
    measure => measure.objectiveId === objectiveId
  );
  const currentTotalWeighting = applicableMeasures.reduce((acc, cur) => cur.weighting + acc, 0);

  if (currentTotalWeighting + weighting > 1) {
    throw new InvalidUpdateError(
      'New/changed measure would yield a total weighting for objective greater than 1'
    );
  }
}

/**
 * Returns a new model instance with a new measure (with data if provided)
 * @param {Model} model model instance
 * @param {MeasureData} measure optional measure data to store as measure
 * @return {Model} updated model instance
 */
export function newMeasure(model: Model, measure?: MeasureData, id?: string): Model {
  const newMeasureId = id || nanoId();

  // Default new measure
  let measureData = {
    id: newMeasureId,
    name: `New Measure ${++newCount}`,
    objectiveId: getObjectives(model)[0].id,
    weighting: 0,
    dataOwner: '',
    unitOfMeasure: getProgrammeUnitOfMeasure(model),
    baselineStart: getProgrammeStart(model).format(DATE_FORMAT),
    baselineComplete: getPhases(model)
      .reduce(
        (acc, cur) => (moment(cur.start).isAfter(acc) ? moment(cur.start) : acc),
        getProgrammeStart(model)
      )
      .add(1, 'year')
      .format(DATE_FORMAT),
    baselineValue: 0,
    change: 0,
    best: 0,
    worst: 0,
    standard: 0,
    minimumStandard: 0,
    definition: '',
  };

  if (measure) {
    // Check validity of measure data
    checkMeasureData(measure);

    // Override default measure data
    measureData = { ...measureData, ...measure };

    checkTotalWeighting(model, measureData.objectiveId, measureData.weighting);
  }

  const { balancedScorecard } = getProgramme(model);
  const newBalancedScorecard = balancedScorecard.map(item => {
    return {
      ...item,
      values: [
        ...item.values,
        {
          id: newMeasureId,
          value: measureData.baselineValue,
        },
      ],
    };
  });

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        measures: [...model.programmes[0].measures, measureData],
        balancedScorecard: newBalancedScorecard,
      },
    ],
  };
}

function escapeRegExp(regexString) {
  return regexString.replace(/[-/\\^$*+?.()|[\]{}]/g, '\\$&');
}

export function replaceMeasureNames(
  measure: Measure,
  equation: string,
  type: 'rename' | 'delete',
  newName?: string
) {
  const standardVariable = `ms_${measure.name.replace(/ /gi, '_')}`;
  const baselineVariable = `mb_${measure.name.replace(/ /gi, '_')}`;
  const changeVariable = `mc_${measure.name.replace(/ /gi, '_')}`;

  const standardRegex = new RegExp(escapeRegExp(standardVariable), 'g');
  const baselineRegex = new RegExp(escapeRegExp(baselineVariable), 'g');
  const changeRegex = new RegExp(escapeRegExp(changeVariable), 'g');

  let newEquation;
  if (type === 'rename' && newName) {
    const newVariableName = newName.replace(/ /gi, '_');

    newEquation = equation.replace(standardRegex, `ms_${newVariableName}`);
    newEquation = newEquation.replace(baselineRegex, `mb_${newVariableName}`);
    newEquation = newEquation.replace(changeRegex, `mc_${newVariableName}`);
  } else if (type === 'delete') {
    const { standard, baselineValue, change } = measure;

    newEquation = equation.replace(standardRegex, `${standard}`);
    newEquation = newEquation.replace(baselineRegex, `${baselineValue}`);
    newEquation = newEquation.replace(changeRegex, `${change}`);
  } else {
    throw new InconsistentArgumentError('Unknown measure thread update type');
  }
  return newEquation;
}

/**
 * Removes a measure with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id measure id
 * @returns {Model} updated model instance
 */
export function removeMeasureById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'null'
): Model {
  const found = model.programmes[0].measures.find(m => m.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No measure with id '${id}' was contained in the model instance`
    );
  }

  // Update cause and effects
  let modifiedModel;
  switch (onDelete) {
    case 'null':
      const { causeAndEffect } = getProgramme(model);
      modifiedModel = causeAndEffect
        .filter(t => t.measureId === id)
        .reduce((acc, cur) => updateThreadById(acc, cur.id, { measureId: 'none' }), model);
      modifiedModel = causeAndEffect.reduce(
        (acc, cur) =>
          updateThreadById(acc, cur.id, {
            equation: replaceMeasureNames(found, getThreadById(acc, cur.id).equation, 'delete'),
          }),
        modifiedModel
      );
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when removing measure');
  }

  // Update balanced scorecard
  const { balancedScorecard } = getProgramme(model);
  const newBalancedScorecard = balancedScorecard.map(item => {
    return {
      ...item,
      values: item.values.filter(entry => entry.id !== id),
    };
  });

  // Update themes
  const { themes } = getProgramme(model);
  const newThemes = themes.map(theme => {
    return {
      ...theme,
      measures: theme.measures.filter(measureId => measureId !== id),
    };
  });

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        measures: modifiedModel.programmes[0].measures.filter(m => m.id !== id),
        balancedScorecard: newBalancedScorecard,
        themes: newThemes,
      },
    ],
  };
}

/**
 * Sets a measure's attributes by id
 * @param {Model} model model instance
 * @param {string} id measure id
 * @param {MeasureData} measure new measure
 * @return {Model} updated model instance
 */
export function updateMeasureById(model: Model, id: string, measure: MeasureData): Model {
  // Find the existing measure to update
  const existingMeasure = getMeasureById(model, id);

  // Get either new or existing objective id and weighting
  const objectiveId = measure.objectiveId || existingMeasure.objectiveId;
  const weighting = measure.weighting === 0 ? 0 : measure.weighting || existingMeasure.weighting;

  // Check validity of data
  checkMeasureData(measure);
  checkTotalWeighting(model, objectiveId, weighting);

  // Ensure an objective exists if updating objective id
  if (measure.hasOwnProperty('objectiveId') && measure.objectiveId !== 'none') {
    const foundObjective = model.programmes[0].objectives.find(o => o.id === measure.objectiveId);
    if (!foundObjective) {
      throw new InvalidUpdateError('Tried to update measure with objective which does not exist');
    }
  }

  // Retrieve cause and effect to update threads
  const { causeAndEffect } = getProgramme(model);

  // Define a modified model
  let modifiedModel = model;

  // Update equation variables if measure name has changed
  if (measure.hasOwnProperty('name') && measure.name !== existingMeasure.name) {
    // Iteratively update model
    modifiedModel = causeAndEffect.reduce((acc, cur) => {
      // Determine new equation
      const newEquation = replaceMeasureNames(
        existingMeasure,
        getThreadById(acc, cur.id).equation,
        'rename',
        measure.name
      );

      // Return update model as accumulator
      return updateThreadById(acc, cur.id, { equation: newEquation });
    }, model);
  }

  // Update measure details with modified model
  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        measures: modifiedModel.programmes[0].measures.map(m =>
          m.id === id
            ? {
                ...m,
                name: measure.hasOwnProperty('name') ? measure.name : m.name,
                objectiveId: measure.hasOwnProperty('objectiveId')
                  ? measure.objectiveId
                  : m.objectiveId,
                weighting: measure.hasOwnProperty('weighting') ? measure.weighting : m.weighting,
                dataOwner: measure.hasOwnProperty('dataOwner') ? measure.dataOwner : m.dataOwner,
                unitOfMeasure: measure.hasOwnProperty('unitOfMeasure')
                  ? measure.unitOfMeasure
                  : m.unitOfMeasure,
                baselineStart: measure.hasOwnProperty('baselineStart')
                  ? measure.baselineStart
                  : m.baselineStart,
                baselineComplete: measure.hasOwnProperty('baselineComplete')
                  ? measure.baselineComplete
                  : m.baselineComplete,
                baselineValue: measure.hasOwnProperty('baselineValue')
                  ? measure.baselineValue
                  : m.baselineValue,
                best: measure.hasOwnProperty('best') ? measure.best : m.best,
                worst: measure.hasOwnProperty('worst') ? measure.worst : m.worst,
                standard: measure.hasOwnProperty('standard') ? measure.standard : m.standard,
                minimumStandard: measure.hasOwnProperty('minimumStandard')
                  ? measure.minimumStandard
                  : m.minimumStandard,
                change: measure.hasOwnProperty('change') ? measure.change : m.change,
                definition: measure.hasOwnProperty('definition')
                  ? measure.definition
                  : m.definition,
              }
            : m),
      },
    ],
  };
}
