// @flow
// API Functions
import { getProgramme } from '../Programme';

// Model Errors
import { InvalidModelError, InconsistentArgumentError, InvalidUpdateError } from '../../errors';

// Flow Types
import type { Model } from '../../types';

/**
 * Returns the element at the provided location in the matrix (referenced by id)
 * @param {Model} model model instance
 * @param {Array<string>} location array of string ids indicating location within matrix
 * @return {number} entry at location
 **/
export function getBenefitAttributionEntry(model: Model, location: Array<string>): number {
  const programme = getProgramme(model);
  if (!programme.benefitAttribution) {
    throw new InvalidModelError("The programme did not contain the 'benefitAttribution' attribute");
  } else if (!programme.benefitAttribution[location[0]]) {
    throw new InconsistentArgumentError(
      `The programme 'benefitAttribution' did not contain a key for ${location[0]}`
    );
  } else if (!programme.benefitAttribution[location[0]][location[1]]) {
    throw new InconsistentArgumentError(
      `The programme 'benefitAttribution' did not contain the secondary key ${location[1]}`
    );
  } else if (!programme.benefitAttribution[location[0]][location[1]][location[2]]) {
    throw new InconsistentArgumentError(
      `The programme 'benefitAttribution' did not contain the tertiary key ${location[1]}`
    );
  } else if (
    typeof programme.benefitAttribution[location[0]][location[1]][location[2]] === 'number'
  ) {
    throw new InvalidModelError(
      `Entry at location ${location[0]}-${location[1]}-${location[2]} was not a number`
    );
  } else if (Number.isNaN(programme.benefitAttribution[location[0]][location[1]][location[2]])) {
    throw new InvalidModelError(
      `Entry at location ${location[0]}-${location[1]}-${location[2]} was NaN (not a number)`
    );
  }

  return programme.benefitAttribution[location[0]][location[1]][location[2]];
}

/**
 * Update the benefit attribution in the model with a type-checked set of new values
 * @param {Model} model model instance
 * @param {Matrix3D} values values to update with
 * @return {Model} updated model instance
 */
export function setBenefitAttribution(
  model: Model,
  values: { [string]: { [string]: { [string]: number } } }
): Model {
  // Throw error if values is undefined
  if (!values) {
    throw new InvalidUpdateError('Tried to update benefit attribution with undefined values');
  }

  // Get programme instance
  const programme = getProgramme(model);

  // Throw error if benefitAttribution not defined
  if (!programme.benefitAttribution) {
    throw new InvalidModelError("The programme did not contain the 'benefitAttribution' attribute");
  }

  // Extract benefit ids from provided values
  const benefitIds = Object.keys(values);
  const benefitIdsValid = benefitIds.reduce(
    (acc, id) => acc && programme.benefits.map(b => b.id).includes(id),
    true
  );
  if (!benefitIdsValid || benefitIds.length !== programme.benefits.length) {
    throw new InvalidUpdateError('The benefit keys in the provided update values were invalid');
  }

  // Extract deliverable ids from provided values
  const deliverableIds = Object.keys(values[benefitIds[0]]);
  const deliverableIdsValid = deliverableIds.reduce(
    (acc, id) => acc && programme.deliverables.map(d => d.id).includes(id),
    true
  );
  if (!deliverableIdsValid || deliverableIds.length !== programme.deliverables.length) {
    throw new InvalidUpdateError('The deliverable keys in the provided update values were invalid');
  }

  // Extract phase ids from provided values
  const phaseIds = Object.keys(values[benefitIds[0]][deliverableIds[0]]);
  const phaseIdsValid = phaseIds.reduce(
    (acc, id) => acc && programme.phases.map(p => p.id).includes(id),
    true
  );
  if (!phaseIdsValid || phaseIds.length !== programme.phases.length) {
    throw new InvalidUpdateError('The phase keys in the provided update values were invalid');
  }

  // Create a new object which we will insert type checked values
  const newMatrix = {};

  // Loop each benefit id...
  benefitIds.forEach(bId => {
    // Declare an object to store benefit data in
    const newBenefitObject = {};

    deliverableIds.forEach(dId => {
      // Declare an object to store deliverable data in
      const newDeliverableObject = {};

      phaseIds.forEach(pId => {
        // If value is properly defined and a number update matrix
        if (
          values[bId][dId] &&
          typeof values[bId][dId][pId] === 'number' &&
          !Number.isNaN(values[bId][dId][pId]) &&
          values[bId][dId][pId] <= 1 &&
          values[bId][dId][pId] >= 0
        ) {
          // Set deliverable object phase key to values
          newDeliverableObject[pId] = values[bId][dId][pId];
        } else if (!values[bId][dId]) {
          // Throw error if deliverable object not defined for this benefit
          throw new InvalidUpdateError(
            'Tried to update benefit attribution when values did not have the correct deliverable keys'
          );
        } else if (!values[bId][dId][pId]) {
          // Throw error if phase object not defined for this benefit x deliverable
          throw new InvalidUpdateError(
            'Tried to update benefit attribution when values did not have the correct phase keys'
          );
        } else if (
          typeof values[bId][dId][pId] !== 'number' ||
          Number.isNaN(values[bId][dId][pId])
        ) {
          // Throw error if value was not a number (NaN or different type)
          throw new InvalidUpdateError(
            'Tried to update benefit attribution when some values were not a number'
          );
        } else {
          throw new InvalidUpdateError(
            'Tried to update benefit attribution when some values were not a percentage [0-1]'
          );
        }
      });
      // Update the benefit object with the deliverable object
      newBenefitObject[dId] = newDeliverableObject;
    });
    // Update the matrix object with the benefit object
    newMatrix[bId] = newBenefitObject;
  });

  // Finally, return new model instance with benefit attribution swapped out
  return {
    ...model,
    programmes: [
      {
        ...programme,
        benefitAttribution: newMatrix,
      },
    ],
  };
}
