// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { getMeasureById } from '../Measure';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Theme, ThemeData, Model } from '../../types';

let newCount = 0;

/**
 * Returns the array of themes from the programme instance
 * @param {Model} model model instance
 * @return {Array<Theme>} array of stakeholders
 **/
export function getThemes(model: Model): Array<Theme> {
  const programme = getProgramme(model);
  if (!programme.themes) {
    throw new InvalidModelError('Programme did not contain an array of themes');
  } else if (!Array.isArray(programme.themes)) {
    throw new InvalidModelError("Programme 'themes' was not an array");
  }
  return programme.themes;
}

/**
 * Returns a specific theme from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id theme id
 * @return {Theme} theme with provided id
 **/
export function getThemeById(model: Model, id: string): Theme {
  const themes = getThemes(model);
  const found = themes.find(theme => theme.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find theme in programme with id ${id}`);
  }

  return found;
}

function checkThemeData(theme: ThemeData, model: Model): boolean {
  if (theme.hasOwnProperty('name')) {
    if (typeof theme.name !== 'string' || theme.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided theme update value name was not a string (or it was empty)'
      );
    }
  }
  if (theme.hasOwnProperty('measures')) {
    if (!Array.isArray(theme.measures)) {
      throw new InvalidUpdateError('Provided theme update value measures was not an array');
    }

    theme.measures.forEach(measureId => {
      getMeasureById(model, measureId);
    });
  }
  return true;
}

/**
 * Returns a new model instance with a new theme (with data if provided)
 * @param {Model} model model instance
 * @param {ThemeData} theme optional theme data to store as theme
 * @return {Model} updated model instance
 */
export function newTheme(model: Model, theme?: ThemeData, id?: string): Model {
  // Create new theme id
  const newThemeId = id || nanoId();

  // Default new theme
  let themeData = {
    id: newThemeId,
    name: `New Theme ${++newCount}`,
    measures: [],
  };

  if (theme) {
    // Check validity of theme data
    checkThemeData(theme, model);

    // Override default theme data
    themeData = { ...themeData, ...theme };
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        themes: [...model.programmes[0].themes, themeData],
      },
    ],
  };
}

/**
 * Removes a theme with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id theme id
 * @returns {Model} updated model instance
 */
export function removeThemeById(model: Model, id: string): Model {
  if (!model.programmes[0].themes.find(s => s.id === id)) {
    throw new InconsistentArgumentError(
      `No theme with id '${id}' was contained in the model instance`
    );
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        themes: model.programmes[0].themes.filter(s => s.id !== id),
      },
    ],
  };
}

/**
 * Sets a theme's attributes by id
 * @param {Model} model model instance
 * @param {string} id theme id
 * @param {ThemeData} theme new theme
 * @return {Model} updated model instance
 */
export function updateThemeById(model: Model, id: string, theme: ThemeData): Model {
  const found = model.programmes[0].themes.find(s => s.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No theme with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkThemeData(theme, model);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        themes: model.programmes[0].themes.map(t =>
          t.id === id
            ? {
                ...t,
                name: theme.hasOwnProperty('name') ? theme.name : t.name,
                measures: theme.hasOwnProperty('measures') ? theme.measures : t.measures,
              }
            : t),
      },
    ],
  };
}
