// @flow
// Schema validator
import Ajv from 'ajv';

// Model schema
import SCHEMA from '../../schema/model.schema.json';

// Flow types
import { type Model } from '../types';

// Initialise schema validator
const ajv = new Ajv({ allErrors: true });
const validate = ajv.compile(SCHEMA);

function isValid(model: Model): { valid: boolean, errors: Array<Object> } {
  // Validate provided model
  const valid = validate(model);
  const errors = Array.isArray(validate.errors) ? validate.errors : [];
  return { valid, errors };
}

export default isValid;
