// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { updateThreadById } from '../Thread';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Group, GroupData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of groups from the programme instance
 * @param {Model} model model instance
 * @return {Array<Group>} array of groups
 **/
export function getGroups(model: Model): Array<Group> {
  const programme = getProgramme(model);
  if (!programme.groups) {
    throw new InvalidModelError('Programme did not contain an array of groups');
  } else if (!Array.isArray(programme.groups)) {
    throw new InvalidModelError("Programme 'groups' was not an array");
  }
  return programme.groups;
}

/**
 * Returns a specific group from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id group id
 * @return {Group} group with provided id
 **/
export function getGroupById(model: Model, id: string): Group {
  const groups = getGroups(model);
  const found = groups.find(group => group.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find group in programme with id ${id}`);
  }

  return found;
}

function checkGroupData(group: GroupData): boolean {
  if (group.hasOwnProperty('name')) {
    if (typeof group.name !== 'string' || group.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided group update value name was not a string (or it was empty)'
      );
    }
  }
  if (group.hasOwnProperty('definition')) {
    if (typeof group.definition !== 'string') {
      throw new InvalidUpdateError('Provided group update value definition was not a string ');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new group (with data if provided)
 * @param {Model} model model instance
 * @param {GroupData} group optional group data to store as group
 * @return {Model} updated model instance
 */
export function newGroup(model: Model, group?: GroupData, id?: string): Model {
  // Default new group
  let groupData = {
    id: id || nanoId(),
    name: `New Group ${++newCount}`,
    definition: '',
  };

  if (group) {
    // Check validity of group data
    checkGroupData(group);

    // Override default group data
    groupData = { ...groupData, ...group };
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        groups: [...model.programmes[0].groups, groupData],
      },
    ],
  };
}

/**
 * Removes a group with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id group id
 * @returns {Model} updated model instance
 */
export function removeGroupById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'null'
): Model {
  if (!model.programmes[0].groups.find(g => g.id === id)) {
    throw new InconsistentArgumentError(
      `No group with id '${id}' was contained in the model instance`
    );
  }

  // Update cause and effects
  let modifiedModel;
  switch (onDelete) {
    case 'null':
      const { causeAndEffect } = getProgramme(model);
      modifiedModel = causeAndEffect
        .filter(t => t.groupId === id)
        .reduce((acc, cur) => updateThreadById(acc, cur.id, { groupId: 'none' }), model);
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when removing group');
  }

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        groups: modifiedModel.programmes[0].groups.filter(g => g.id !== id),
      },
    ],
  };
}

/**
 * Sets a group's attributes by id
 * @param {Model} model model instance
 * @param {string} id group id
 * @param {GroupData} group new group
 * @return {Model} updated model instance
 */
export function updateGroupById(model: Model, id: string, group: GroupData): Model {
  const found = model.programmes[0].groups.find(g => g.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No group with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkGroupData(group);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        groups: model.programmes[0].groups.map(g =>
          g.id === id
            ? {
                ...g,
                name: group.hasOwnProperty('name') ? group.name : g.name,
                definition: group.hasOwnProperty('definition') ? group.definition : g.definition,
              }
            : g),
      },
    ],
  };
}
