// @flow
// Validation functions
import isValid from './validation';
import isConsistent from './consistency';

// Errors
import { InvalidStateError, InconsistentStateError } from '../errors';

// Flow types
import { type Model } from '../types';

// Define function to confirm whether state is valid using schema
function checkModel(model: Model, operation: string) {
  // Validate provided model
  const { valid, errors: validationErrors } = isValid(model);

  // If model is not valid throw error
  if (!valid) {
    // Provide descriptions
    console.error(`${operation} yielded an invalid model, details below:`);
    validationErrors.forEach(console.error);
    console.error('throwing model', model);

    // Throw error
    throw new InvalidStateError(`${operation} yielded an invalid model`);
  }

  // Check provided model for consistency
  const { valid: consistent, errors: consistencyErrors } = isConsistent(model);

  // If model is not consistent throw error
  if (!consistent) {
    // Provide descriptions
    consistencyErrors.forEach(err => console.error(err));

    // Throw error
    throw new InconsistentStateError(`${operation} yielded an inconsistent model`);
  }

  // Otherwise, return valid model
  return model;
}

export default checkModel;
