// @flow
import React from 'react';
import type { Node } from 'react';

// Spinner and Text blueprint components
import { Spinner, Text } from '@blueprintjs/core';

// Local Flow Type
type LoadingProps = {
  text: string,
};

// Styling
const containerStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  height: '100%',
};

const innerStyle = {
  display: 'flex',
  flex: 1,
  alignItems: 'center',
  justifyContent: 'center',
  flexDirection: 'column',
  maxWidth: '60%',
};

// Stateless functional component
export const Loading = ({ text }: LoadingProps): Node => {
  return (
    <div style={containerStyle}>
      <div style={innerStyle}>
        {/* Spinner */}
        <Spinner />

        {/* Accompanying text */}
        <div style={{ marginTop: 10 }}>
          <Text>{text}</Text>
        </div>
      </div>
    </div>
  );
};

export default Loading;
