// @flow
// ID Generator
import nanoId from 'nanoid';

// Evaluator function
import safeEval from 'safe-eval';

// API Functions
import { getProgramme } from '../Programme';
import { getBeneficiaries } from '../Beneficiary';
import { getGroups } from '../Group';
import { getBenefits } from '../Benefit';
import { getDeliverables } from '../Deliverable';
import { getMeasures } from '../Measure';
import { getStakeholders } from '../Stakeholder';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Reference Generator
import { nextRef } from '../../../../utils/utils';

// Constants
import {
  PARAMETER_CHANGE_TYPE_ARITHMETIC,
  PARAMETER_CHANGE_TYPE_GEOMETRIC,
} from '../../../../config/constants';

// Flow Types
import type { Thread, ThreadData, Model } from '../../types';

/**
 * Returns the array of threads from the programme instance
 * @param {Model} model model instance
 * @return {Array<Thread>} array of threads
 **/
export function getThreads(model: Model): Array<Thread> {
  const programme = getProgramme(model);
  if (!programme.causeAndEffect) {
    throw new InvalidModelError('Programme did not contain an array of threads');
  } else if (!Array.isArray(programme.causeAndEffect)) {
    throw new InvalidModelError("Programme 'causeAndEffect' was not an array");
  }
  return programme.causeAndEffect;
}

/**
 * Returns a specific thread from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id thread id
 * @return {Thread} thread with provided id
 **/
export function getThreadById(model: Model, id: string): Thread {
  const threads = getThreads(model);
  const found = threads.find(thread => thread.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find thread in programme with id ${id}`);
  }

  return found;
}

function checkThreadData(thread: ThreadData, model: Model): boolean {
  if (thread.hasOwnProperty('reference')) {
    if (typeof thread.reference !== 'string' || thread.reference.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value reference was not a string (or it was empty)'
      );
    }
  }
  if (thread.hasOwnProperty('benefitId')) {
    if (typeof thread.benefitId !== 'string' || thread.benefitId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value benefitId was not a string (or it was empty)'
      );
    }
    const found = getBenefits(model).find(benefit => benefit.id === thread.benefitId);
    if (!found) {
      throw new InvalidUpdateError('Tried to update thread benefit id to a non-existent benefit');
    }
  }
  if (thread.hasOwnProperty('deliverableId')) {
    if (typeof thread.deliverableId !== 'string' || thread.deliverableId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value deliverableId was not a string (or it was empty)'
      );
    }
    const found = getDeliverables(model).find(
      deliverable => deliverable.id === thread.deliverableId
    );
    if (!found) {
      throw new InvalidUpdateError(
        'Tried to update thread deliverable id to a non-existent deliverable'
      );
    }
  }
  if (thread.hasOwnProperty('measureId')) {
    if (typeof thread.measureId !== 'string' || thread.measureId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value measureId was not a string (or it was empty)'
      );
    } else if (thread.measureId !== 'none') {
      const found = getMeasures(model).find(measure => measure.id === thread.measureId);
      if (!found) {
        throw new InvalidUpdateError('Tried to update thread measure id to a non-existent measure');
      }
    }
  }
  if (thread.hasOwnProperty('stakeholderId')) {
    if (typeof thread.stakeholderId !== 'string' || thread.stakeholderId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value stakeholderId was not a string (or it was empty)'
      );
    }
    const found = getStakeholders(model).find(
      stakeholder => stakeholder.id === thread.stakeholderId
    );
    if (!found) {
      throw new InvalidUpdateError(
        'Tried to update thread stakeholder id to a non-existent stakeholder'
      );
    }
  }
  if (thread.hasOwnProperty('groupId')) {
    if (typeof thread.groupId !== 'string' || thread.groupId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value groupId was not a string (or it was empty)'
      );
    } else if (thread.groupId !== 'none') {
      const found = getGroups(model).find(group => group.id === thread.groupId);
      if (!found) {
        throw new InvalidUpdateError('Tried to update thread group id to a non-existent group');
      }
    }
  }
  if (thread.hasOwnProperty('beneficiaryId')) {
    if (typeof thread.beneficiaryId !== 'string' || thread.beneficiaryId.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value beneficiaryId was not a string (or it was empty)'
      );
    } else if (thread.beneficiaryId !== 'none') {
      const found = getBeneficiaries(model).find(
        beneficiary => beneficiary.id === thread.beneficiaryId
      );
      if (!found) {
        throw new InvalidUpdateError(
          'Tried to update thread beneficiary id to a non-existent beneficiary'
        );
      }
    }
  }
  if (thread.hasOwnProperty('cause')) {
    if (typeof thread.cause !== 'string') {
      throw new InvalidUpdateError('Provided thread update value cause was not a string');
    }
  }
  if (thread.hasOwnProperty('effect')) {
    if (typeof thread.effect !== 'string') {
      throw new InvalidUpdateError('Provided thread update value effect was not a string');
    }
  }
  if (thread.hasOwnProperty('equation')) {
    if (typeof thread.equation !== 'string' || thread.equation.length === 0) {
      throw new InvalidUpdateError(
        'Provided thread update value equation was not a string (or it was empty)'
      );
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new thread (with data if provided)
 * @param {Model} model model instance
 * @param {ThreadData} thread optional thread data to store as thread
 * @return {Model} updated model instance
 */
export function newThread(model: Model, thread?: ThreadData, id?: string): Model {
  const newThreadId = id || nanoId();

  // Default new thread
  let threadData = {
    id: newThreadId,
    reference: nextRef(),
    benefitId: getBenefits(model)[0].id,
    deliverableId: getDeliverables(model)[0].id,
    measureId: 'none',
    stakeholderId: getStakeholders(model)[0].id,
    groupId: 'none',
    beneficiaryId: 'none',
    cause: '',
    effect: '',
    equation: '0',
  };

  if (thread) {
    // Check validity of thread data
    checkThreadData(thread, model);

    // Override default thread data
    threadData = { ...threadData, ...thread };
  }

  const newModel = {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        causeAndEffect: [...model.programmes[0].causeAndEffect, threadData],
      },
    ],
  };

  return updateThreadValues(newModel, newThreadId);
}

/**
 * Removes a thread with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id thread id
 * @returns {Model} updated model instance
 */
export function removeThreadById(model: Model, id: string): Model {
  if (!model.programmes[0].causeAndEffect.find(t => t.id === id)) {
    throw new InconsistentArgumentError(
      `No thread with id '${id}' was contained in the model instance`
    );
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        causeAndEffect: model.programmes[0].causeAndEffect.filter(t => t.id !== id),
        _threadValues: model.programmes[0]._threadValues.filter(tv => tv.id !== id),
      },
    ],
  };
}

/**
 * Sets a thread's attributes by id
 * @param {Model} model model instance
 * @param {string} id thread id
 * @param {ThreadData} thread new thread
 * @return {Model} updated model instance
 */
export function updateThreadById(model: Model, id: string, thread: ThreadData): Model {
  // Check validity of data
  checkThreadData(thread, model);

  const newModel = {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        causeAndEffect: model.programmes[0].causeAndEffect.map(t =>
          t.id === id
            ? {
                ...t,
                reference: thread.hasOwnProperty('reference') ? thread.reference : t.reference,
                benefitId: thread.hasOwnProperty('benefitId') ? thread.benefitId : t.benefitId,
                deliverableId: thread.hasOwnProperty('deliverableId')
                  ? thread.deliverableId
                  : t.deliverableId,
                measureId: thread.hasOwnProperty('measureId') ? thread.measureId : t.measureId,
                stakeholderId: thread.hasOwnProperty('stakeholderId')
                  ? thread.stakeholderId
                  : t.stakeholderId,
                groupId: thread.hasOwnProperty('groupId') ? thread.groupId : t.groupId,
                beneficiaryId: thread.hasOwnProperty('beneficiaryId')
                  ? thread.beneficiaryId
                  : t.beneficiaryId,
                cause: thread.hasOwnProperty('cause') ? thread.cause : t.cause,
                effect: thread.hasOwnProperty('effect') ? thread.effect : t.effect,
                equation: thread.hasOwnProperty('equation') ? thread.equation : t.equation,
              }
            : t),
      },
    ],
  };

  if (thread.hasOwnProperty('equation')) {
    return updateThreadValues(newModel, id);
  }

  return newModel;
}

function toUnderscore(val: string): string {
  return val.replace(/ /gi, '_');
}

function generateContextObject(model: Model, year: number): { [string]: number } {
  const programme = getProgramme(model);
  const { parameters, measures, stakeholders, stakeholderBenefits, _coreBenefits } = programme;

  // Context object
  const returnObject = {};

  // Calculate dynamic parameters
  parameters.forEach(parameter => {
    // Initial value
    let value = parameter.value;

    if (parameter.dynamic) {
      for (let y = 0; y < year; y++) {
        if (parameter.changeType === PARAMETER_CHANGE_TYPE_ARITHMETIC) {
          value += parameter.change;
        } else if (parameter.changeType === PARAMETER_CHANGE_TYPE_GEOMETRIC) {
          value *= parameter.change;
        }
      }
    }

    // Add parameter to context object
    returnObject[toUnderscore(parameter.name)] = value;
  });

  // Add measure standard
  measures.forEach(
    measure => (returnObject[`ms_${toUnderscore(measure.name)}`] = measure.standard)
  );

  // Add measure baseline
  measures.forEach(
    measure => (returnObject[`mb_${toUnderscore(measure.name)}`] = measure.baselineValue)
  );

  // Add measure change
  measures.forEach(measure => (returnObject[`mc_${toUnderscore(measure.name)}`] = measure.change));

  // Add stakeholder revenue datum
  stakeholders.forEach(
    stakeholder =>
      (returnObject[`sr_${toUnderscore(stakeholder.name)}`] =
        stakeholderBenefits[_coreBenefits[0]][stakeholder.id])
  );

  // Add stakeholder margin datum
  stakeholders.forEach(
    stakeholder =>
      (returnObject[`sm_${toUnderscore(stakeholder.name)}`] =
        stakeholderBenefits[_coreBenefits[2]][stakeholder.id])
  );

  return returnObject;
}

export function evaluateThread(model: Model, id: string, year: number): number {
  const equation = getThreadById(model, id).equation;

  // console.log('evaluating equation: ', equation);
  let answer = 0;
  try {
    const context = generateContextObject(model, year);
    answer = safeEval(equation, context);
  } catch (e) {
    answer = 0;
  }
  return answer;
}

export function updateThreadValues(model: Model, id: string): Model {
  const years = model._analysisYears;
  const { _threadValues: threadValues } = getProgramme(model);

  const filteredThreadValues = threadValues.filter(tv => tv.id !== id);

  const yearsArray = [...Array(years).keys()];
  const values = yearsArray.map(y => evaluateThread(model, id, y));

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        _threadValues: [
          ...filteredThreadValues,
          {
            id,
            values,
          },
        ],
      },
    ],
  };
}
