// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { getPhases } from '../Phase';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Constants
import { COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT } from '../../../../config/constants';

// Flow Types
import type { Cost, CostData, Model } from '../../types';

let newCount = 0;

/**
 * Returns the array of costs from the programme instance
 * @param {Model} model model instance
 * @return {Array<Cost>} array of costs
 **/
export function getCosts(model: Model): Array<Cost> {
  const programme = getProgramme(model);
  if (!programme.costs) {
    throw new InvalidModelError('Programme did not contain an array of costs');
  } else if (!Array.isArray(programme.costs)) {
    throw new InvalidModelError("Programme 'costs' was not an array");
  }
  return programme.costs;
}

/**
 * Returns a specific cost from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id cost id
 * @return {Cost} cost with provided id
 **/
export function getCostById(model: Model, id: string): Cost {
  const costs = getCosts(model);
  const found = costs.find(cost => cost.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find cost in programme with id ${id}`);
  }

  return found;
}

function checkCostData(cost: CostData): boolean {
  if (cost.hasOwnProperty('name')) {
    if (typeof cost.name !== 'string' || cost.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided cost update values for name was not a string (or it was empty)'
      );
    }
  }
  if (cost.hasOwnProperty('type')) {
    if (
      typeof cost.type !== 'string' ||
      ![COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT].includes(cost.type)
    ) {
      throw new InvalidUpdateError(
        'Provided cost update values for type was not a string (or it was not a predefined type)'
      );
    }
  }
  if (cost.hasOwnProperty('definition')) {
    if (typeof cost.definition !== 'string') {
      throw new InvalidUpdateError('Provided cost update values for definition was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new cost (with data if provided)
 * @param {Model} model model instance
 * @param {CostData} cost optional cost data to store as cost
 * @return {Model} updated model instance
 */
export function newCost(model: Model, cost?: CostData, id?: string): Model {
  const newCostId = id || nanoId();

  // Default new cost
  let costData = {
    id: newCostId,
    name: `New Cost ${++newCount}`,
    type: COST_TYPE_DEVELOPMENT,
    definition: '',
  };

  if (cost) {
    // Check validity of cost data
    checkCostData(cost);

    // Override default cost data
    costData = { ...costData, ...cost };
  }

  // Update Cost Attribution
  const { costAttribution } = getProgramme(model);

  const phases = getPhases(model);

  const newCostAttribution = {
    ...costAttribution,
    [newCostId]: phases.reduce((acc, cur) => {
      acc[cur.id] = 0;
      return acc;
    }, {}),
  };

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        costs: [...model.programmes[0].costs, costData],
        costAttribution: newCostAttribution,
      },
    ],
  };
}

/**
 * Removes a cost with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id cost id
 * @returns {Model} updated model instance
 */
export function removeCostById(model: Model, id: string): Model {
  if (!model.programmes[0].costs.find(c => c.id === id)) {
    throw new InconsistentArgumentError(
      `No cost with id '${id}' was contained in the model instance`
    );
  }

  // Update cost attribution
  const updatedCosts = getCosts(model).filter(c => c.id !== id);
  const { costAttribution, phases } = getProgramme(model);
  const newCostAttribution = updatedCosts.reduce((acc, cost) => {
    acc[cost.id] = phases.reduce((acc, phase) => {
      acc[phase.id] = costAttribution[cost.id][phase.id];
      return acc;
    }, {});
    return acc;
  }, {});

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        costs: model.programmes[0].costs.filter(c => c.id !== id),
        costAttribution: newCostAttribution,
      },
    ],
  };
}

/**
 * Sets a cost's attributes by id
 * @param {Model} model model instance
 * @param {string} id cost id
 * @param {CostData} cost new cost
 * @return {Model} updated model instance
 */
export function updateCostById(model: Model, id: string, cost: CostData): Model {
  const found = model.programmes[0].costs.find(cost => cost.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No cost with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkCostData(cost);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        costs: model.programmes[0].costs.map(c =>
          c.id === id
            ? {
                ...c,
                name: cost.hasOwnProperty('name') ? cost.name : c.name,
                type: cost.hasOwnProperty('type') ? cost.type : c.type,
                definition: cost.hasOwnProperty('definition') ? cost.definition : c.definition,
              }
            : c),
      },
    ],
  };
}
