// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme } from '../Programme';
import { getBenefits } from '../Benefit';
import { removeThreadById } from '../Thread';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Flow Types
import type { Stakeholder, StakeholderData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of stakeholders from the programme instance
 * @param {Model} model model instance
 * @return {Array<Stakeholder>} array of stakeholders
 **/
export function getStakeholders(model: Model): Array<Stakeholder> {
  const programme = getProgramme(model);
  if (!programme.stakeholders) {
    throw new InvalidModelError('Programme did not contain an array of stakeholders');
  } else if (!Array.isArray(programme.stakeholders)) {
    throw new InvalidModelError("Programme 'stakeholders' was not an array");
  }
  return programme.stakeholders;
}

/**
 * Returns a specific stakeholder from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id stakeholder id
 * @return {Stakeholder} stakeholder with provided id
 **/
export function getStakeholderById(model: Model, id: string): Stakeholder {
  const stakeholders = getStakeholders(model);
  const found = stakeholders.find(stakeholder => stakeholder.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find stakeholder in programme with id ${id}`);
  }

  return found;
}

function checkStakeholderData(stakeholder: StakeholderData): boolean {
  if (stakeholder.hasOwnProperty('name')) {
    if (typeof stakeholder.name !== 'string' || stakeholder.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided stakeholder update value name was not a string (or it was empty)'
      );
    }
  }
  if (stakeholder.hasOwnProperty('owner')) {
    if (typeof stakeholder.owner !== 'string') {
      throw new InvalidUpdateError('Provided stakeholder update value owner was not a string');
    }
  }
  if (stakeholder.hasOwnProperty('budgetRef')) {
    if (typeof stakeholder.budgetRef !== 'string') {
      throw new InvalidUpdateError('Provided stakeholder update value budgetRef was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new stakeholder (with data if provided)
 * @param {Model} model model instance
 * @param {StakeholderData} stakeholder optional stakeholder data to store as stakeholder
 * @return {Model} updated model instance
 */
export function newStakeholder(model: Model, stakeholder?: StakeholderData, id?: string): Model {
  // Create new stakeholder id
  const newStakeholderId = id || nanoId();

  // Default new stakeholder
  let stakeholderData = {
    id: newStakeholderId,
    name: `New Stakeholder ${++newCount}`,
    owner: '',
    budgetRef: '',
  };

  if (stakeholder) {
    // Check validity of stakeholder data
    checkStakeholderData(stakeholder);

    // Override default stakeholder data
    stakeholderData = { ...stakeholderData, ...stakeholder };
  }

  // Update benefit attribution, cost attribution, and benefit buildup
  const { stakeholderBenefits } = getProgramme(model);
  const benefits = getBenefits(model);

  const newStakeholderBenefits = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = {
      ...stakeholderBenefits[benefit.id],
      [newStakeholderId]: 0,
    };
    return acc;
  }, {});

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        stakeholders: [...model.programmes[0].stakeholders, stakeholderData],
        stakeholderBenefits: newStakeholderBenefits,
      },
    ],
  };
}

/**
 * Removes a stakeholder with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id stakeholder id
 * @returns {Model} updated model instance
 */
export function removeStakeholderById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'cascade'
): Model {
  if (!model.programmes[0].stakeholders.find(s => s.id === id)) {
    throw new InconsistentArgumentError(
      `No stakeholder with id '${id}' was contained in the model instance`
    );
  }

  // Update cause and effects
  let modifiedModel;
  switch (onDelete) {
    case 'cascade':
      const { causeAndEffect } = getProgramme(model);
      modifiedModel = causeAndEffect
        .filter(t => t.stakeholderId === id)
        .reduce((acc, cur) => removeThreadById(acc, cur.id), model);
      break;
    default:
      throw new InconsistentArgumentError('Unrecognised delete strategy when removing stakeholder');
  }

  // Update stakeholder benefits
  const updatedStakeholders = getStakeholders(model).filter(s => s.id !== id);
  const { stakeholderBenefits, benefits } = getProgramme(model);
  const newStakeholderBenefits = benefits.reduce((acc, benefit) => {
    acc[benefit.id] = updatedStakeholders.reduce((acc, stakeholder) => {
      acc[stakeholder.id] = stakeholderBenefits[benefit.id][stakeholder.id];
      return acc;
    }, {});
    return acc;
  }, {});

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        stakeholders: modifiedModel.programmes[0].stakeholders.filter(s => s.id !== id),
        stakeholderBenefits: newStakeholderBenefits,
      },
    ],
  };
}

/**
 * Sets a stakeholder's attributes by id
 * @param {Model} model model instance
 * @param {string} id stakeholder id
 * @param {StakeholderData} stakeholder new stakeholder
 * @return {Model} updated model instance
 */
export function updateStakeholderById(
  model: Model,
  id: string,
  stakeholder: StakeholderData
): Model {
  const found = model.programmes[0].stakeholders.find(s => s.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No stakeholder with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkStakeholderData(stakeholder);

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        stakeholders: model.programmes[0].stakeholders.map(s =>
          s.id === id
            ? {
                ...s,
                name: stakeholder.hasOwnProperty('name') ? stakeholder.name : s.name,
                owner: stakeholder.hasOwnProperty('owner') ? stakeholder.owner : s.owner,
                budgetRef: stakeholder.hasOwnProperty('budgetRef')
                  ? stakeholder.budgetRef
                  : s.budgetRef,
              }
            : s),
      },
    ],
  };
}
