// @flow
// ID Generator
import nanoId from 'nanoid';

// API Functions
import { getProgramme, getProgrammeUnitOfMeasure } from '../Programme';
import { getThreadById, updateThreadById } from '../Thread';

// Model Errors
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';

// Constants
import {
  PARAMETER_CHANGE_TYPE_ARITHMETIC,
  PARAMETER_CHANGE_TYPE_GEOMETRIC,
} from '../../../../config/constants';

// Flow Types
import type { Parameter, ParameterData, Model } from '../../types';

let newCount = 0;

/**
 * Returns the array of parameters from the programme instance
 * @param {Model} model model instance
 * @return {Array<Parameter>} array of parameters
 **/
export function getParameters(model: Model): Array<Parameter> {
  const programme = getProgramme(model);
  if (!programme.parameters) {
    throw new InvalidModelError('Programme did not contain an array of parameters');
  } else if (!Array.isArray(programme.parameters)) {
    throw new InvalidModelError("Programme 'parameters' was not an array");
  }
  return programme.parameters;
}

/**
 * Returns a specific parameter from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id parameter id
 * @return {Parameter} parameter with provided id
 **/
export function getParameterById(model: Model, id: string): Parameter {
  const parameters = getParameters(model);
  const found = parameters.find(parameter => parameter.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find parameter in programme with id ${id}`);
  }

  return found;
}

function checkParameterData(parameter: ParameterData): boolean {
  if (parameter.hasOwnProperty('name')) {
    if (typeof parameter.name !== 'string' || parameter.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided parameter update value name was not a string (or it was empty)'
      );
    }
  }
  if (parameter.hasOwnProperty('unitOfMeasure')) {
    if (typeof parameter.unitOfMeasure !== 'string') {
      throw new InvalidUpdateError(
        'Provided parameter update value unitOfMeasure was not a string'
      );
    }
  }
  if (parameter.hasOwnProperty('value')) {
    if (typeof parameter.value !== 'number' || Number.isNaN(parameter.value)) {
      throw new InvalidUpdateError('Provided parameter update value value was not a number');
    }
  }
  if (parameter.hasOwnProperty('dynamic')) {
    if (typeof parameter.dynamic !== 'boolean') {
      throw new InvalidUpdateError(
        'Provided parameter update value dynamic was not a boolean value'
      );
    }
  }
  if (parameter.hasOwnProperty('change')) {
    if (typeof parameter.change !== 'number' || Number.isNaN(parameter.change)) {
      throw new InvalidUpdateError('Provided parameter update value change was not a number');
    }
  }
  if (parameter.hasOwnProperty('changeType')) {
    if (
      typeof parameter.changeType !== 'string' ||
      ![PARAMETER_CHANGE_TYPE_ARITHMETIC, PARAMETER_CHANGE_TYPE_GEOMETRIC].includes(
        parameter.changeType
      )
    ) {
      throw new InvalidUpdateError(
        'Provided parameter update value changeType was not a string (or it was not of predefined type)'
      );
    }
  }
  if (parameter.hasOwnProperty('definition')) {
    if (typeof parameter.definition !== 'string') {
      throw new InvalidUpdateError('Provided parameter update value definition was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new parameter (with data if provided)
 * @param {Model} model model instance
 * @param {ParameterData} parameter optional parameter data to store as parameter
 * @return {Model} updated model instance
 */
export function newParameter(model: Model, parameter?: ParameterData, id?: string): Model {
  // Default new parameter
  let parameterData = {
    id: id || nanoId(),
    name: `p_ParameterName${++newCount}`,
    unitOfMeasure: getProgrammeUnitOfMeasure(model),
    value: 0,
    dynamic: false,
    change: 0,
    changeType: PARAMETER_CHANGE_TYPE_ARITHMETIC,
    definition: '',
  };

  if (parameter) {
    // Check validity of parameter data
    checkParameterData(parameter);

    // Override default parameter data
    parameterData = { ...parameterData, ...parameter };
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        parameters: [...model.programmes[0].parameters, parameterData],
      },
    ],
  };
}

function escapeRegExp(regexString) {
  return regexString.replace(/[-/\\^$*+?.()|[\]{}]/g, '\\$&');
}

function replaceParameterNames(
  parameter: Parameter,
  equation: string,
  type: 'rename' | 'delete',
  newName?: string
) {
  const variable = parameter.name;
  const variableRegExp = escapeRegExp(variable);
  let newEquation;
  if (type === 'delete') {
    const { value } = parameter;
    newEquation = equation.replace(variableRegExp, `${value}`);
  } else if (type === 'rename') {
    if (typeof newName !== 'string') {
      throw new InconsistentArgumentError(
        'New name was not provided when updating parameter names in cause and effect threads'
      );
    }
    newEquation = equation.replace(variableRegExp, newName);
  }
  return newEquation;
}

/**
 * Removes a parameter with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id parameter id
 * @returns {Model} updated model instance
 */
export function removeParameterById(model: Model, id: string): Model {
  const found = model.programmes[0].parameters.find(p => p.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No parameter with id '${id}' was contained in the model instance`
    );
  }

  // Update cause and effects
  let modifiedModel;
  const { causeAndEffect } = getProgramme(model);
  modifiedModel = causeAndEffect.reduce((acc, cur) => {
    const newEquation = replaceParameterNames(found, getThreadById(acc, cur.id).equation, 'delete');
    if (typeof newEquation !== 'string') {
      throw new InconsistentArgumentError('Removed parameter but new equation was not a string');
    }
    return updateThreadById(acc, cur.id, { equation: newEquation });
  }, model);

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        parameters: modifiedModel.programmes[0].parameters.filter(p => p.id !== id),
      },
    ],
  };
}

/**
 * Sets a parameter's attributes by id
 * @param {Model} model model instance
 * @param {string} id parameter id
 * @param {ParameterData} parameter new parameter
 * @return {Model} updated model instance
 */
export function updateParameterById(model: Model, id: string, parameter: ParameterData): Model {
  // Check validity of data
  checkParameterData(parameter);

  const { causeAndEffect } = getProgramme(model);
  const existingParameter = getParameterById(model, id);
  let modifiedModel = model;
  // Update equation variables if measure name has changed
  if (parameter.hasOwnProperty('name') && parameter.name !== existingParameter.name) {
    modifiedModel = causeAndEffect.reduce((acc, cur) => {
      const newEquation = replaceParameterNames(
        existingParameter,
        getThreadById(acc, cur.id).equation,
        'rename',
        parameter.name
      );
      if (typeof newEquation !== 'string') {
        throw new InconsistentArgumentError(
          'Updated parameter names but new equation was not a string'
        );
      }
      return updateThreadById(acc, cur.id, { equation: newEquation });
    }, model);
  }

  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        parameters: modifiedModel.programmes[0].parameters.map(p =>
          p.id === id
            ? {
                ...p,
                name: parameter.hasOwnProperty('name') ? parameter.name : p.name,
                unitOfMeasure: parameter.hasOwnProperty('unitOfMeasure')
                  ? parameter.unitOfMeasure
                  : p.unitOfMeasure,
                value: parameter.hasOwnProperty('value') ? parameter.value : p.value,
                dynamic: parameter.hasOwnProperty('dynamic') ? parameter.dynamic : p.dynamic,
                change: parameter.hasOwnProperty('change') ? parameter.change : p.change,
                changeType: parameter.hasOwnProperty('changeType')
                  ? parameter.changeType
                  : p.changeType,
                definition: parameter.hasOwnProperty('definition')
                  ? parameter.definition
                  : p.definition,
              }
            : p),
      },
    ],
  };
}
