// @flow
import nanoId from 'nanoid';
import { getProgramme, getProgrammeUnitOfMeasure } from '../Programme';
import { getDeliverables } from '../Deliverable';
import { getPhases } from '../Phase';
import { getStakeholders } from '../Stakeholder';
import { getThreads, removeThreadById } from '../Thread';
import { InvalidModelError, InvalidUpdateError, InconsistentArgumentError } from '../../errors';
import { BENEFIT_TYPE_NON_FINANCIAL, BENEFIT_TYPE_FINANCIAL } from '../../../../config/constants';
import type { Benefit, BenefitData, Model, DeleteStrategy } from '../../types';

let newCount = 0;

/**
 * Returns the array of benefits from the programme instance
 * @param {Model} model model instance
 * @return {Array<Benefit>} array of benefits
 **/
export function getBenefits(model: Model): Array<Benefit> {
  const programme = getProgramme(model);
  if (!programme.benefits) {
    throw new InvalidModelError('Programme did not contain an array of benefits');
  } else if (!Array.isArray(programme.benefits)) {
    throw new InvalidModelError("Programme 'benefits' was not an array");
  }
  return programme.benefits;
}

/**
 * Returns a specific benefit from the programme instance based on provided id
 * @param {Model} model model instance
 * @param {string} id benefit id
 * @return {Benefit} benefit with provided id
 **/
export function getBenefitById(model: Model, id: string): Benefit {
  const benefits = getBenefits(model);
  const found = benefits.find(benefit => benefit.id === id);

  if (!found) {
    throw new InconsistentArgumentError(`Could not find benefit in programme with id ${id}`);
  }

  return found;
}

function checkBenefitData(benefit: BenefitData): boolean {
  if (benefit.hasOwnProperty('name')) {
    if (typeof benefit.name !== 'string' || benefit.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided benefit update value name was not a string (or it was empty)'
      );
    }
  }
  if (benefit.hasOwnProperty('type')) {
    if (
      typeof benefit.type !== 'string' ||
      ![BENEFIT_TYPE_FINANCIAL, BENEFIT_TYPE_NON_FINANCIAL].includes(benefit.type)
    ) {
      throw new InvalidUpdateError(
        'Provided benefit update value type was not a string (or it was not one of predefined types)'
      );
    }
  }
  if (benefit.hasOwnProperty('unitOfMeasure')) {
    if (typeof benefit.unitOfMeasure !== 'string' || benefit.unitOfMeasure.length === 0) {
      throw new InvalidUpdateError(
        'Provided benefit update value unitOfMeasure was not a string (or it was empty)'
      );
    }
  }
  if (benefit.hasOwnProperty('definition')) {
    if (typeof benefit.definition !== 'string') {
      throw new InvalidUpdateError('Provided benefit update value definition was not a string');
    }
  }
  return true;
}

/**
 * Returns a new model instance with a new benefit (with data if provided)
 * @param {Model} model model instance
 * @param {BenefitData} benefit optional benefit data to store as benefit
 * @return {Model} updated model instance
 */
export function newBenefit(model: Model, benefit?: BenefitData, id?: string): Model {
  const newBenefitId = id || nanoId();
  // Default new benefit
  let benefitData = {
    id: newBenefitId,
    name: `New Benefit ${++newCount}`,
    type: BENEFIT_TYPE_NON_FINANCIAL,
    unitOfMeasure: getProgrammeUnitOfMeasure(model),
    definition: '',
  };

  if (benefit) {
    // Check validity of benefit data
    checkBenefitData(benefit);

    // Override default benefit data
    benefitData = { ...benefitData, ...benefit };
  }

  // Update benefit attribution, buildup, and stakeholder benefits;
  const { benefitAttribution, benefitBuildup, stakeholderBenefits } = getProgramme(model);
  const [deliverables, phases, stakeholders] = [
    getDeliverables(model),
    getPhases(model),
    getStakeholders(model),
  ];

  const newBenefitAttribution = {
    ...benefitAttribution,
    [newBenefitId]: deliverables.reduce((acc, cur) => {
      acc[cur.id] = phases.reduce((acc, cur) => {
        acc[cur.id] = 0;
        return acc;
      }, {});
      return acc;
    }, {}),
  };

  const newBenefitBuildup = {
    ...benefitBuildup,
    [newBenefitId]: phases.reduce((acc, cur) => {
      acc[cur.id] = 0;
      return acc;
    }, {}),
  };

  const newStakeholderBenefits = {
    ...stakeholderBenefits,
    [newBenefitId]: stakeholders.reduce((acc, cur) => {
      acc[cur.id] = 0;
      return acc;
    }, {}),
  };

  // Update benefit buildup sensitivity and benefit sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    benefitBuildupSensitivity: {
      ...scenario.benefitBuildupSensitivity,
      [newBenefitId]: phases.reduce((acc, phase) => {
        acc[phase.id] = 0;
        return acc;
      }, {}),
    },
    benefitSensitivity: [
      ...scenario.benefitSensitivity,
      {
        id: newBenefitId,
        switch: true,
        sensitivity: 0,
      },
    ],
  }));

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        benefits: [...model.programmes[0].benefits, benefitData],
        benefitAttribution: newBenefitAttribution,
        benefitBuildup: newBenefitBuildup,
        stakeholderBenefits: newStakeholderBenefits,
        scenarios: newScenarios,
      },
    ],
  };
}

/**
 * Removes a benefit with provided in from model instance
 * @param {Model} model model instance
 * @param {string} id benefit id
 * @returns {Model} updated model instance
 */
export function removeBenefitById(
  model: Model,
  id: string,
  onDelete: ?DeleteStrategy = 'cascade'
): Model {
  // Check model exists, core benefits exists, and whether to remove is core benefit
  if (!model.programmes[0].benefits.find(b => b.id === id)) {
    throw new InconsistentArgumentError(
      `No benefit with id '${id}' was contained in the model instance`
    );
  } else if (
    !model.programmes[0]._coreBenefits ||
    !Array.isArray(model.programmes[0]._coreBenefits) ||
    model.programmes[0]._coreBenefits.length !== 3
  ) {
    throw new InvalidModelError('Core benefits array was not present in programme');
  } else if (model.programmes[0]._coreBenefits.includes(id)) {
    throw new InvalidUpdateError(`Cannot remove core benefit ${id}`);
  }

  // Modify cause and effect threads before removing benefit
  let modifiedModel;
  const causeAndEffect = getThreads(model);
  switch (onDelete) {
    case 'cascade':
      modifiedModel = causeAndEffect
        .filter(t => t.benefitId === id)
        .reduce((acc, cur) => removeThreadById(acc, cur.id), model);
      break;
    default:
      throw new InconsistentArgumentError(
        'Unknown onDelete strategy when removing benefit from model'
      );
  }

  // Update benefit attribution, benefit buildup, and stakeholder benefits
  const {
    benefitAttribution,
    benefitBuildup,
    stakeholderBenefits,
    deliverables,
    phases,
    stakeholders,
  } = getProgramme(model);

  // Filter out benefit to remove
  const updatedBenefits = getBenefits(model).filter(b => b.id !== id);

  // Calculate new benefit attribtuion
  const newBenefitAttribution = updatedBenefits.reduce((acc, benefit) => {
    // Reconstruct new benefit attribution from old one
    acc[benefit.id] = deliverables.reduce((acc, deliverable) => {
      acc[deliverable.id] = phases.reduce((acc, phase) => {
        acc[phase.id] = benefitAttribution[benefit.id][deliverable.id][phase.id];
        return acc;
      }, {});
      return acc;
    }, {});
    return acc;
  }, {});

  // Calculate new benefit buildup
  const newBenefitBuildup = updatedBenefits.reduce((acc, benefit) => {
    acc[benefit.id] = phases.reduce((acc, phase) => {
      acc[phase.id] = benefitBuildup[benefit.id][phase.id];
      return acc;
    }, {});
    return acc;
  }, {});

  // Calculate new cost attribution
  const newStakeholderBenefits = updatedBenefits.reduce((acc, benefit) => {
    acc[benefit.id] = stakeholders.reduce((acc, stakeholder) => {
      acc[stakeholder.id] = stakeholderBenefits[benefit.id][stakeholder.id];
      return acc;
    }, {});
    return acc;
  }, {});

  // Update benefit buildup sensitivity, and benefit sensitivities
  const { scenarios } = getProgramme(model);
  const newScenarios = scenarios.map(scenario => ({
    ...scenario,
    benefitBuildupSensitivity: updatedBenefits.reduce((acc, benefit) => {
      acc[benefit.id] = phases.reduce((acc, phase) => {
        acc[phase.id] = scenario.benefitBuildupSensitivity[benefit.id][phase.id];
        return acc;
      }, {});
      return acc;
    }, {}),
    benefitSensitivity: scenario.benefitSensitivity.filter(bs => bs.id !== id),
  }));

  // Return modified model with benefit removed
  return {
    ...modifiedModel,
    programmes: [
      {
        ...modifiedModel.programmes[0],
        benefits: modifiedModel.programmes[0].benefits.filter(b => b.id !== id),
        benefitAttribution: newBenefitAttribution,
        benefitBuildup: newBenefitBuildup,
        stakeholderBenefits: newStakeholderBenefits,
        scenarios: newScenarios,
      },
    ],
  };
}

/**
 * Sets a benefit's attributes by id
 * @param {Model} model model instance
 * @param {string} id benefit id
 * @param {BenefitData} benefit new benefit
 * @return {Model} updated model instance
 */
export function updateBenefitById(model: Model, id: string, benefit: BenefitData): Model {
  const found = model.programmes[0].benefits.find(benefit => benefit.id === id);
  if (!found) {
    throw new InconsistentArgumentError(
      `No benefit with id '${id}' was contained in the model instance`
    );
  }

  // Check validity of data
  checkBenefitData(benefit);

  // Check if trying to update core benefit to non financial
  if (
    model.programmes[0]._coreBenefits.includes(id) &&
    benefit.hasOwnProperty('type') &&
    benefit.type !== BENEFIT_TYPE_FINANCIAL
  ) {
    throw new InvalidUpdateError(`Cannot update core benefit ${id} to a non financial benefit`);
  }

  return {
    ...model,
    programmes: [
      {
        ...model.programmes[0],
        benefits: model.programmes[0].benefits.map(b =>
          b.id === id
            ? {
                ...b,
                name: benefit.hasOwnProperty('name') ? benefit.name : b.name,
                type: benefit.hasOwnProperty('type') ? benefit.type : b.type,
                unitOfMeasure: benefit.hasOwnProperty('unitOfMeasure')
                  ? benefit.unitOfMeasure
                  : b.unitOfMeasure,
                definition: benefit.hasOwnProperty('definition')
                  ? benefit.definition
                  : b.definition,
              }
            : b),
      },
    ],
  };
}
