// @flow
// API Functions
import { getProgramme } from '../Programme';

// Model Errors
import { InvalidModelError, InconsistentArgumentError, InvalidUpdateError } from '../../errors';

// Flow Types
import type { Model } from '../../types';

/**
 * Returns the element at the provided location in the matrix (referenced by id)
 * @param {Model} model model instance
 * @param {Array<string>} location array of string ids indicating location within matrix
 * @return {number} entry at location
 **/
export function getStakeholderBenefitsEntry(model: Model, location: Array<string>): number {
  const programme = getProgramme(model);
  if (!programme.stakeholderBenefits) {
    throw new InvalidModelError(
      "The programme did not contain the 'stakeholderBenefits' attribute"
    );
  } else if (!programme.stakeholderBenefits[location[0]]) {
    throw new InconsistentArgumentError(
      `The programme 'stakeholderBenefits' did not contain a key for ${location[0]}`
    );
  } else if (!programme.stakeholderBenefits[location[0]][location[1]]) {
    throw new InconsistentArgumentError(
      `The programme 'stakeholderBenefits' did not contain the secondary key ${location[1]}`
    );
  } else if (typeof programme.stakeholderBenefits[location[0]][location[1]] === 'number') {
    throw new InvalidModelError(`Entry at location ${location[0]}-${location[1]} was not a number`);
  } else if (Number.isNaN(programme.stakeholderBenefits[location[0]][location[1]])) {
    throw new InvalidModelError(
      `Entry at location ${location[0]}-${location[1]} was NaN (not a number)`
    );
  }

  return programme.stakeholderBenefits[location[0]][location[1]];
}

/**
 * Update the stakeholder benefits in the model with a type-checked set of new values
 * @param {Model} model model instance
 * @param {Matrix2D} values values to update with
 * @return {Model} updated model instance
 */
export function setStakeholderBenefits(
  model: Model,
  values: { [string]: { [string]: number } }
): Model {
  // Throw error if values is undefined
  if (!values) {
    throw new InvalidUpdateError('Tried to update stakeholder benefits with undefined values');
  }

  // Get programme instance
  const programme = getProgramme(model);

  // Throw error if stakeholderBenefits not defined
  if (!programme.stakeholderBenefits) {
    throw new InvalidModelError(
      "The programme did not contain the 'stakeholderBenefits' attribute"
    );
  }

  // Extract benefit ids from provided values
  const benefitIds = Object.keys(values);
  const benefitIdsValid = benefitIds.reduce(
    (acc, id) => acc && programme.benefits.map(b => b.id).includes(id),
    true
  );
  if (!benefitIdsValid || benefitIds.length !== programme.benefits.length) {
    throw new InvalidUpdateError('The benefit keys in the provided update values were invalid');
  }

  // Extract stakeholder ids from provided values
  const stakeholderIds = Object.keys(values[benefitIds[0]]);
  const stakeholderIdsValid = stakeholderIds.reduce(
    (acc, id) => acc && programme.stakeholders.map(i => i.id).includes(id),
    true
  );
  if (!stakeholderIdsValid || stakeholderIds.length !== programme.stakeholders.length) {
    throw new InvalidUpdateError('The stakeholder keys in the provided update values were invalid');
  }

  // Create a new object which we will insert type checked values
  const newMatrix = {};

  // Loop each benefit id...
  benefitIds.forEach(bId => {
    // Declare an object to store benefit data in
    const newBenefitObject = {};

    stakeholderIds.forEach(sId => {
      // If value is properly defined and a number update matrix
      if (typeof values[bId][sId] === 'number' && !Number.isNaN(values[bId][sId])) {
        // Set benefit object stakeholder key to values
        newBenefitObject[sId] = values[bId][sId];
      } else if (!values[bId][sId]) {
        // Throw error if stakeholder object not defined for this benefit
        throw new InvalidUpdateError(
          'Tried to update stakeholder benefits when values did not have the correct stakeholder keys'
        );
      } else {
        // Throw error if value was not a number (NaN or different type)
        throw new InvalidUpdateError(
          'Tried to update stakeholder benefits when some values were not a number'
        );
      }
    });

    // Update the matrix object with the benefit object
    newMatrix[bId] = newBenefitObject;
  });

  // Finally, return new model instance with benefit buildup swapped out
  return {
    ...model,
    programmes: [
      {
        ...programme,
        stakeholderBenefits: newMatrix,
      },
    ],
  };
}
