import React from 'react';
import ReactDOM from 'react-dom';

// Redux dependencies
import { createStore, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';

import HOSPITAL from './model/examples/hospital.json';

// Redux Persist dependencies
// import { persistStore, persistReducer } from 'redux-persist';
// import { PersistGate } from 'redux-persist/integration/react';
// import storage from 'redux-persist/lib/storage'; // localstorage

// Redux Middleware
import thunk from 'redux-thunk';
import { createLogger } from 'redux-logger';

// Redux development tools
import { composeWithDevTools } from 'redux-devtools-extension';

// Global Stylesheet
import './index.css';

// React app
import App from './App';
// import Loading from './components/Loading';
import * as serviceWorker from './utils/serviceWorker';
import rootReducer from './reducers';

// Plural library
import pluralize from 'pluralize';

// Flow Types
import { type GetState, type Action } from './actions/types';
import { COST_TYPE_DEVELOPMENT, COST_TYPE_SUPPORT } from './config/constants.js';
import createRandomScorecardValues from './utils/createRandomScorecardValues.js';

const newBenefitSchedule = {};
HOSPITAL.programmes[0].scenarios.forEach(scenario => {
  newBenefitSchedule[scenario.id] = new Array(4 * 8).fill(0);
});

console.log(newBenefitSchedule);

const newCostSchedule = {};
HOSPITAL.programmes[0].scenarios.forEach(scenario => {
  newCostSchedule[scenario.id] = new Array(4 * 8).fill(0);
});

console.log(newCostSchedule);

// Configure Plural Library
pluralize.addSingularRule(/beneficiaries$/i, 'beneficiary');
pluralize.addPluralRule(/beneficiary$/i, 'beneficiaries');

const filterLogs = (getState: GetState, action: Action) => {
  if (typeof action === 'undefined') {
    return true;
  } else {
    return true;
  }
};

// Configure Redux Logger
const logger = createLogger({
  predicate: filterLogs,
  diff: true,
  collapsed: () => true,
  actionTransformer: (action: Action) =>
    typeof action === 'function' ? { type: 'THUNK_DISPATCH', originalAction: action } : action,
});

// Redux Persisted state configuration and reducer
// NOTE: TURNED OFF the persist functionality whilst in active development
// TODO: Need to decide what get's persisted and what doesn't as ids could change now on load
// const persistConfig = {
//   key: 'root',
//   storage,
//   blacklist: ['auth', 'editor'],
// };
// const persistedReducer = persistReducer(persistConfig, rootReducer);

// Redux Middleware
const middleware = [];
middleware.push(logger); // Logs to console
middleware.push(thunk); // Allows thunks

// Redux create store with persisted reducer, dev tools, and middlewares
// NB: Uncomment the next line and comment out line after to add persisted reducer
// const store = createStore(persistedReducer, composeWithDevTools(applyMiddleware(...middleware)));
const store = createStore(rootReducer, composeWithDevTools(applyMiddleware(...middleware)));

// Create the persistor function
// NB: Uncomment this line and uncomment persist gate in app render to add persisted reducer
// const persistor = persistStore(store);

// Import the WebAssembly library and then render
// Wasm modules need to be loaded async
// https://github.com/webpack/webpack/issues/6615
import('api')
  .then(() => {
    // Render the Application with store, showing spinner whilst rehydrating
    ReactDOM.render(
      <Provider store={store}>
        {/* <PersistGate
          loading={<Loading text={'Loading Value Management Toolset'} />}
          persistor={persistor}
        > */}
        <App />
        {/* </PersistGate> */}
      </Provider>,
      document.getElementById('root')
    );
  })
  .catch(console.error);

// TODO: Offline first
serviceWorker.unregister();
