import { DCF_GRAPH } from '../config/constants';
import { SET_SELECTED_GRAPH, SET_COST_OF_CAPITAL } from '../actions/types';

const initialState = {
  selected: DCF_GRAPH,
  costOfCapital: 0.1,
};

export default (state = initialState, action = {}) => {
  switch (action.type) {
    case SET_SELECTED_GRAPH:
      return {
        ...state,
        selected: action.graph,
      };
    case SET_COST_OF_CAPITAL:
      return {
        ...state,
        costOfCapital: action.value,
      };
    default:
      return state;
  }
};
