// @flow

/**
 * Thrown when a function was passed an invalid model instance
 */
export class InvalidModelError extends Error {
  // Specific message is passed to parent Error
  constructor(message: string) {
    super(message);
    this.name = 'Invalid Model Error';
    this.description = 'The throwing function was passed an invalid model instance';
  }
}

/**
 * Thrown when a function's intended operation would produce an invalid model instance
 */
export class InvalidUpdateError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'Invalid Update Error';
    this.description =
      'The throwing function attempted to update a model value with an invalid one';
  }
}

/**
 * Thrown when the argument provided to a function was invalid (e.g. id not present in model)
 */
export class InconsistentArgumentError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'Invalid Argument Error';
    this.description =
      'The throwing function was provided an argument which was invalid with respect to the model instance';
  }
}

/**
 * Throws when the application encounters an inconsistent state
 */
export class InconsistentStateError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'Inconsistent State Error';
    this.description = 'The model instance stored within the VMT entered an inconsistent state';
  }
}

/**
 * Throws when the application encounters an invalid state
 */
export class InvalidStateError extends Error {
  constructor(message: string) {
    super(message);
    this.name = 'Invalid State Error';
    this.description = 'The model instance stored within the VMT entered an invalid state';
  }
}
