export function validatePhase(data: types.Phase, model: types.Model) {
  let errors = {};

  // Check equired fields
  const required = ['name', 'start', 'slip'];
  checkNonEmptyString(required, data, errors);

  if (!errors.name) {
    checkUniqueName(data, API.getPhases(model), 'phase', errors);
  }

  // If start date is alright so far...
  if (!errors.start) {
    const startMoment = moment(data.start, DATE_FORMAT, true);

    // Check date format is correct
    if (!startMoment.isValid()) {
      errors.start = 'The date was in an invlaid format';
    } else {
      // Retreive programme start
      const programmeStart = API.getProgrammeStart(model);

      // Calculate analysis end date
      const analysisEnd = moment(programmeStart).add(model._analysisYears, 'years');

      // Confirm phase start is after programme start
      if (startMoment.isBefore(programmeStart)) {
        errors.start = `The phase start date should occur on or after the programme start date (${programmeStart.format(
          DATE_FORMAT
        )})`;
      } else if (startMoment.isAfter(analysisEnd)) {
        errors.start = 'The phase start date is after the period of analysis ends';
      }
    }
  }

  // Check duration is defined and zero or a positivie number
  if (data.duration < 0) {
    errors.duration = 'Duration cannot be negative';
  } else if (data.duration !== 0 && !data.duration) {
    errors.duration = 'This field is required';
  }

  // Check duration does not mean phase exceeds analysis duration
  if (!errors.start && !errors.duration) {
    const analysisEndMoment = API.getProgrammeStart(model).add(model._analysisYears, 'years');
    const phaseEndMoment = moment(data.start, DATE_FORMAT, true).add(data.duration, 'weeks');

    if (phaseEndMoment.isAfter(analysisEndMoment)) {
      errors.duration = 'Phase duration means it will end after the analysis period';
    }
  }

  if (!errors.slip) {
    // Valid rules
    const validTypes = [
      CONSTANTS.PHASE_SLIP_RULE_FIXED,
      CONSTANTS.PHASE_SLIP_RULE_TIME_AND_MATERIALS,
    ];

    // Check slip rule is one of defined types
    if (!validTypes.includes(data.slip)) {
      errors.slip = 'The phase slip rule was not one of the two pre-defined types';
    }
  }

  return errors;
}

export function warnPhase(data: types.Phase) {
  let warnings = {};
  return warnings;
}

