export function validateParameter(data: types.Parameter, model: types.Model) {
  let errors = {};

  // Check required fields are present
  const required = ['name'];
  checkNonEmptyString(required, data, errors);

  // Check for duplicate name
  if (!errors.name) {
    checkUniqueName(data, API.getParameters(model), 'parameter', errors);
  }

  // Check value is present
  if (!data.value && data.value !== 0) {
    errors.value = 'Parameter value is required';
  }

  if (data.dynamic) {
    const required = ['changeType'];
    checkNonEmptyString(required, data, errors);

    // Check change type is one of valid types
    const validTypes = [
      CONSTANTS.PARAMETER_CHANGE_TYPE_ARITHMETIC,
      CONSTANTS.PARAMETER_CHANGE_TYPE_GEOMETRIC,
    ];
    if (!errors.changeType && !validTypes.includes(data.changeType)) {
      errors.changeType = 'Parameter change type was not one of pre-defined types';
    }

    // Check change is supplied
    if (!data.change && data.change !== 0) {
      errors.change = 'Parameter change is required if it is dynamic';
    }
  }

  return errors;
}

export function warnParameter(data: types.Parameter) {
  let warnings = {};

  if (data.unitOfMeasure.length === 0) {
    warnings.unitOfMeasure = 'A unit of measure is highly encouraged';
  }

  return warnings;
}

export function validateThread(data: types.Thread, model: types.Model) {
  let errors = {};

  // Check required fields are present
  const required = [
    'reference',
    'deliverableId',
    'measureId',
    'benefitId',
    'stakeholderId',
    'groupId',
    'beneficiaryId',
    'equation',
  ];
  checkNonEmptyString(required, data, errors);

  // Check reference is unique
  if (!errors.reference) {
    const threadRefs = API.getThreads(model)
      .filter(thread => thread.id !== data.id)
      .map(thread => thread.reference);
    if (threadRefs.includes(data.reference)) {
      errors.reference = 'Thread reference must be unique';
    }
  }

  // Check deliverable exists
  if (!errors.deliverableId) {
    if (data.deliverableId === 'none') {
      errors.deliverableId = 'The deliverable for a thread cannot be None';
    } else {
      const deliverableIds = API.getDeliverables(model).map(deliverable => deliverable.id);
      if (!deliverableIds.includes(data.deliverableId)) {
        errors.deliverableId = 'This deliverable does not seem to exist';
      }
    }
  }

  // Check benefit exists and is not none
  if (!errors.benefitId) {
    if (data.benefitId === 'none') {
      errors.benefitId = 'The benefit for a thread cannot be None';
    } else {
      const benefitIds = API.getBenefits(model).map(benefit => benefit.id);

      if (!benefitIds.includes(data.benefitId)) {
        errors.benefitId = 'This benefit does not seem to exist';
      }
    }
  }

  // Check stakeholder exists
  if (!errors.stakeholderId) {
    if (data.stakeholderId === 'none') {
      errors.stakeholderId = 'The stakeholder for a thread cannot be None';
    } else {
      const stakeholderIds = API.getStakeholders(model).map(stakeholder => stakeholder.id);
      if (!stakeholderIds.includes(data.stakeholderId)) {
        errors.stakeholderId = 'This stakeholder does not seem to exist';
      }
    }
  }

  // Check measure exists or is none
  if (!errors.measureId && data.measureId !== 'none') {
    const measureIds = API.getMeasures(model).map(measure => measure.id);
    if (!measureIds.includes(data.measureId)) {
      errors.measureId = 'This measure does not seem to exist';
    }
  }

  // Check group exists or is none
  if (!errors.groupId && data.groupId !== 'none') {
    const groupIds = API.getGroups(model).map(group => group.id);
    if (!groupIds.includes(data.groupId)) {
      errors.groupId = 'This group does not seem to exist';
    }
  }

  // Check beneficiary exists or is none
  if (!errors.beneficiaryId && data.beneficiaryId !== 'none') {
    const beneficiaryIds = API.getBeneficiaries(model).map(beneficiary => beneficiary.id);
    if (!beneficiaryIds.includes(data.beneficiaryId)) {
      errors.beneficiaryId = 'This beneficiary does not seem to exist';
    }
  }

  return errors;
}

export function warnThread(data: types.Thread) {
  let warnings = {};

  if (!data.cause) {
    warnings.cause = "Adding a 'cause' description is strongly recommended";
  }

  if (!data.effect) {
    warnings.effect = "Adding a 'effect' description is strongly recommended";
  }

  return warnings;
}

export function validatePerspective(data: types.Perspective, model: types.Model) {
  let errors = {};

  // Check required fields are present
  const required = ['name'];
  checkNonEmptyString(required, data, errors);

  // Check name is unique
  if (!errors.name) {
    checkUniqueName(data, API.getPerspectives(model), 'perspective', errors);
  }

  // Check weighting is at least 0
  if (!data.weighting && data.weighting !== 0) {
    errors.weighting = 'This field is required';
  } else if (data.weighting < 0) {
    errors.weighting = 'Weighting must be at least 0';
  }

  // Check total weighting is not greater than 1
  if (!errors.weighting) {
    const perspectives = API.getPerspectives(model);
    const totalWeighting = perspectives
      .filter(p => p.id !== data.id)
      .reduce((acc, cur) => acc + cur.weighting, 0);

    if (totalWeighting + data.weighting > 1) {
      errors.weighting = 'Total weighting would be greater than 100%, reduce other weights first';
    }
  }

  return errors;
}

export function warnPerspective(data: types.Perspective) {
  let warnings = {};
  return warnings;
}

export function validateObjective(data: types.Objective, model: types.Model) {
  let errors = {};

  // Check required fields are present
  const required = ['name', 'perspectiveId'];
  checkNonEmptyString(required, data, errors);

  // Check name is unique
  if (!errors.name) {
    checkUniqueName(data, API.getObjectives(model), 'objective', errors);
  }

  // Check perspective exists
  if (!errors.perspectiveId) {
    if (data.perspectiveId !== 'none') {
      const perspectiveIds = API.getPerspectives(model).map(perspective => perspective.id);
      if (!perspectiveIds.includes(data.perspectiveId)) {
        errors.perspectiveId = 'This perspective was not found';
      }
    }
  }

  // Check weighting is at least 0
  if (!data.weighting && data.weighting !== 0) {
    errors.weighting = 'This field is required';
  } else if (data.weighting < 0) {
    errors.weighting = 'Weighting must be at least 0';
  }

  // Check total weighting is not greater than 1 w.r.t. perspective id
  if (!errors.weighting) {
    const objectives = API.getObjectives(model);
    const totalWeighting = objectives
      .filter(o => o.id !== data.id)
      .filter(o => o.perspectiveId === data.perspectiveId)
      .reduce((acc, cur) => acc + cur.weighting, 0);

    if (totalWeighting + data.weighting > 1) {
      errors.weighting = 'Total weighting would be greater than 100%, reduce other weights first';
    }
  }

  return errors;
}

export function warnObjective(data: types.Objective) {
  let warnings = {};
  return warnings;
}

export function validateMeasure(data: types.Measure, model: types.Model) {
  let errors = {};

  // Check required fields are present
  const required = ['name', 'objectiveId'];
  checkNonEmptyString(required, data, errors);

  if (!errors.name) {
    checkUniqueName(data, API.getMeasures(model), 'measure', errors);
  }

  // Check objective id is either none or valid
  if (!errors.objectiveId) {
    if (data.objectiveId !== 'none') {
      const objectiveIds = API.getObjectives(model).map(objective => objective.id);
      if (!objectiveIds.includes(data.objectiveId)) {
        errors.objectiveId = 'This objective was not found';
      }
    }
  }

  // Parse dates as moments
  const baselineStartMoment = moment(data.baselineStart, DATE_FORMAT, true);
  const baselineCompleteMoment = moment(data.baselineComplete, DATE_FORMAT, true);

  // Check dates are valid
  if (!baselineStartMoment.isValid()) {
    errors.baselineStart = 'Date is not in valid format';
  }
  if (!baselineCompleteMoment.isValid()) {
    errors.baselineComplete = 'Date is not in valid format';
  }

  // If the dates are valid
  if (!errors.baselineStart && !errors.baselineComplete) {
    // Check whether complete date is before start date
    if (baselineCompleteMoment.isBefore(baselineStartMoment)) {
      errors.baselineComplete = 'Complete date cannot be before start date';
    }
  }

  // Check dates occur on or after scorecard start
  if (!errors.baselineStart) {
    // Retrieve scorecard start date
    const scorecardStartMoment = API.getProgrammeScorecardStart(model);

    if (baselineStartMoment.isBefore(scorecardStartMoment)) {
      errors.baselineStart = 'Start date cannot be before scorecard start date';
    }

    if (baselineCompleteMoment.isBefore(scorecardStartMoment)) {
      errors.baselineStart = 'Complete date cannot be before scorecard start date';
    }
  }

  // Check weighting is at least 0
  if (!data.weighting && data.weighting !== 0) {
    errors.weighting = 'This field is required';
  } else if (data.weighting < 0) {
    errors.weighting = 'Weighting must be at least 0';
  }

  if (!errors.weighting) {
    // Retreive measures to check weighting w.r.t. the measure's objective
    const measures = API.getMeasures(model);

    // Calculate total weighting without current measure
    const totalWeighting = measures
      .filter(m => m.id !== data.id)
      .filter(m => m.objectiveId === data.objectiveId)
      .reduce((acc, cur) => acc + cur.weighting, 0);

    // If new weighting would create total weighting greater than 1
    if (totalWeighting + data.weighting > 1) {
      errors.weighting = 'Total weighting would be greater than 100%, reduce other weights first';
    }
  }

  // Determine upper, lower bound and direction of travel
  let upper, lower, direction;

  // Compare best and worst values
  if (data.best === data.worst && data.best !== 0) {
    errors.best = 'Best cannot be the same as worst value';
    errors.worst = 'Worst cannot be the same as best value';
  } else if (data.best > data.worst) {
    upper = data.best;
    lower = data.worst;
    direction = 'INCREASE';
  } else if (data.best < data.worst) {
    upper = data.worst;
    lower = data.best;
    direction = 'DECREASE';
  } else {
    upper = data.best;
    lower = data.worst;
    direction = 'ZERO';
  }

  if (!errors.best && !errors.worst && upper !== undefined && lower !== undefined) {
    // Destructure values to check
    const { baselineValue, standard, minimumStandard, change } = data;

    // If the best and worst aren't set to 0
    if (direction !== 'ZERO') {
      // Check baseline value is within bounds
      if (baselineValue > upper || baselineValue < lower) {
        errors.baselineValue = 'Value must be between the best and worst values';
      }

      // Check standard
      if (standard > upper || standard < lower) {
        errors.standard = 'Value must be between the best and worst values';
      }

      // Check minimum standard
      if (minimumStandard > upper || minimumStandard < lower) {
        errors.minimumStandard = 'Value must be between the best and worst values';
      }
    }

    if (!errors.minimumStandard && !errors.standard && !errors.change) {
      if (direction === 'INCREASE') {
        // Check change value
        if (change < 0) {
          errors.change =
            'Best value was greater than worst value, thus change should be positive (or zero)';
        }

        // Check minimum standard is lower than standard
        if (minimumStandard > standard) {
          errors.minimumStandard =
            'Best value was greater than worst value, thus minimum standard should be lower than standard';
        }
      } else if (direction === 'DECREASE') {
        // Check change value
        if (change > 0) {
          errors.change =
            'Worst value was greater than best value, thus change should be negative (or zero)';
        }

        // Check minimum standard is greater than standard
        if (minimumStandard < standard) {
          errors.minimumStandard =
            'Worst value was greater than best value, thus minimum standard should be lower than standard';
        }
      }
    }
  }

  return errors;
}

export function warnMeasure(data: types.Measure) {
  let warnings = {};

  if (!data.unitOfMeasure) {
    warnings.unitOfMeasure = 'A unit of measure is highly encouraged';
  }

  return warnings;
}

export function validateProgramme(data: types.Programme, model: types.Model) {
  let errors = {};

  // Check the fields are not empty
  const required = ['name', 'start', 'financialStart', 'scorecardStart', 'unitOfMeasure'];
  checkNonEmptyString(required, data, errors);

  // If the programme start is ok so far...
  if (!errors.start) {
    // Try to parse as moment
    const startMoment = moment(data.start, DATE_FORMAT, true);
    if (!startMoment.isValid()) {
      errors.start = 'Date was in an invalid format';
    } else {
      // Check the programme start is before all phase starts
      model.programmes[0].phases.forEach(phase => {
        if (startMoment.isAfter(phase.start)) {
          errors.start = `Phase '${
            phase.name
          }' would occur after the programme start, change the phase start first`;
        }
      });
    }
  }

  // If the programme financial start is ok so far...
  if (!errors.financialStart) {
    // Try to parse as moment
    const financialStartMoment = moment(data.financialStart, DATE_FORMAT, true);
    if (!financialStartMoment.isValid()) {
      errors.financialStart = 'Date was in an invalid format';
    } else {
      // If there are errors with programme start, don't check it yet
      if (!errors.start) {
        // Check financial start is before programme start
        if (financialStartMoment.isAfter(data.start)) {
          errors.financialStart = 'Financial start date cannot be after programme start date';
        }
      }
    }
  }

  // If the programme scorecard start is ok so far...
  if (!errors.scorecardStart) {
    // Try to parse as moment
    const scorecardStartMoment = moment(data.scorecardStart, DATE_FORMAT, true);
    if (!scorecardStartMoment.isValid()) {
      errors.scorecardStart = 'Date was in an invalid format';
    } else {
      // If there are errors with programme start, don't check it yet
      if (!errors.start) {
        // Check financial start is before programme start
        if (scorecardStartMoment.isAfter(data.start)) {
          errors.scorecardStart = 'Scorecard start date cannot be after programme start date';
        }
      }
    }
  }

  return errors;
}

export function warnProgramme(data: types.Programme) {
  let warnings = {};

  if (!data.mission) {
    warnings.mission = 'Setting a programme mission is highly encouraged';
  }

  if (!data.vision) {
    warnings.vision = 'Setting a programme vision is highly encouraged';
  }

  return warnings;
}

