import { SET_SELECTED_ELEMENT, SET_NODE_EXPANDED, TOGGLE_DARK_MODE } from '../actions/types';

const initialState = {
  explorer: {
    selected: undefined,
    expanded: {},
  },
  benefitAttribution: {
    selectedBenefit: undefined,
  },
  dark: false,
};

export default (state = initialState, action = {}) => {
  switch (action.type) {
    case SET_NODE_EXPANDED:
      return {
        ...state,
        explorer: {
          ...state.explorer,
          expanded: {
            ...state.explorer.expanded,
            [action.id]: action.expanded,
          },
        },
      };
    case SET_SELECTED_ELEMENT:
      return {
        ...state,
        explorer: {
          ...state.explorer,
          selected: {
            ...action.element,
          },
        },
      };
    case TOGGLE_DARK_MODE:
      return {
        ...state,
        dark: !state.dark,
      };
    case 'SET_SELECTED_BENEFIT_ATTRIBUTION':
      return {
        ...state,
        benefitAttribution: {
          selectedBenefit: action.value,
        },
      };
    default:
      return state;
  }
};
