// @flow
import moment from 'moment';

// Model Errors
import { InconsistentArgumentError, InvalidModelError } from '../../errors';

// API Functions
import { getProgramme } from '../Programme';
import { getMeasures } from '../Measure';

// Constants
import { DATE_FORMAT } from '../../../../config/constants';

// Flow Types
import { type Model, type ScorecardItem } from '../../types';

export function getScorecardItem(model: Model, date: string): ScorecardItem {
  const programme = getProgramme(model);
  const { balancedScorecard } = programme;

  const m = moment(date, DATE_FORMAT, true);
  if (!m.isValid()) {
    throw new InconsistentArgumentError(
      `Date ${date} was not in the required format '${DATE_FORMAT}'`
    );
  }

  const found = balancedScorecard.find(item => item.date === date);
  if (!found) {
    throw new InconsistentArgumentError(
      `Balanced scorecard did not contain and item with date ${date}`
    );
  } else {
    return found;
  }
}

export function getScorecardValue(model: Model, date: string, id: string): number {
  const { values } = getScorecardItem(model, date);

  const measures = getMeasures(model);
  const foundMeasure = measures.find(measure => measure.id === id);
  if (!foundMeasure) {
    throw new InconsistentArgumentError('Programme did not contain a measure with the provided id');
  }

  const foundValue = values.find(value => value.id === id);
  if (!foundValue) {
    throw new InvalidModelError('Measure exists but entry in balanced scorecard did not exist');
  }

  return foundValue.value;
}
