// @flow
// Date Time Library
import moment from 'moment';

// API Functions
import { getBenefits } from '../Benefit';

// Model Errors
import { InvalidModelError, InvalidUpdateError } from '../../errors';

// Constants
import { DATE_FORMAT, BENEFIT_TYPE_FINANCIAL } from '../../../../config/constants';

// Flow Types
import type { Model, Programme, ProgrammeData } from '../../types';

// Constants
const programmeAttributes = [
  'id',
  'name',
  'mission',
  'vision',
  'unitOfMeasure',
  'start',
  'financialStart',
  'scorecardStart',
];

/**
 * Returns the (only) programme instance contained within the model
 * @param {Model} model model instance
 * @return {Programme}  programme instance
 */
export function getProgramme(model: Model): Programme {
  if (!model.programmes) {
    throw new InvalidModelError("Provided model did not have a 'programmes' attribute");
  } else if (!Array.isArray(model.programmes)) {
    throw new InvalidModelError("Provided model's 'programmes' attribute was not an array");
  } else if (!model.programmes[0]) {
    throw new InvalidModelError("Provided model's 'programmes[0]' was not defined");
  }

  return model.programmes[0];
}

/**
 * Returns all the (only) programme's non-array attributes
 * @param {Model} model model instance
 * @return {ProgrammeAttributes} object containing all the programme's (non-array) attributes
 */
// NB: Return type flow annotation omitted due to issue with flow recognising moment refinement
export function getProgrammeAttributes(model: Model) {
  const programme = getProgramme(model);
  const attributes = {};

  programmeAttributes.forEach(key => {
    if (!programme[key] && key !== 'mission' && key !== 'vision') {
      throw new InvalidModelError(`Programme did not contain the '${key}' attribute`);
    } else if (typeof programme[key] !== 'string') {
      throw new InvalidModelError(`The programme's '${key}' attribute was not a string`);
    }

    // If attribute is a date attribute then try to parse as moment
    if (['start', 'financialStart', 'scorecardStart'].includes(key)) {
      const m = moment(programme[key], DATE_FORMAT, true);

      // If moment is invalid then throw error
      if (!m.isValid()) {
        throw new InvalidModelError(
          `The programme's 'start' attribute was not in valid date format ${DATE_FORMAT}`
        );
      }

      // Otherwise set moment as attribute
      attributes[key] = m;
    } else {
      // If not date than set raw as attribute
      attributes[key] = programme[key];
    }
  });
  return attributes;
}

function checkProgrammeData(data: ProgrammeData, model: Model): boolean {
  // Check the name attribute if it exists
  if (data.hasOwnProperty('name')) {
    if (typeof data.name !== 'string' || data.name.length === 0) {
      throw new InvalidUpdateError(
        'Provided programme update values did not have name attribute (or it was length 0)'
      );
    }
  }
  // Check the mission attribute if it exists
  if (data.hasOwnProperty('mission')) {
    if (typeof data.mission !== 'string') {
      throw new InvalidUpdateError(
        'Provided programme update values did not have mission attribute'
      );
    }
  }
  // Check the vision attribute if it exists
  if (data.hasOwnProperty('vision')) {
    if (typeof data.vision !== 'string') {
      throw new InvalidUpdateError(
        'Provided programme update values did not have vision attribute'
      );
    }
  }
  // Check the unitOfMeasure attribute if it exists
  if (data.hasOwnProperty('unitOfMeasure')) {
    if (typeof data.unitOfMeasure !== 'string' || data.unitOfMeasure.length === 0) {
      throw new InvalidUpdateError(
        'Provided programme update values did not have unitOfMeasure attribute (or it was length 0)'
      );
    }
  }

  // Check the start attribute if it exists
  let hasProgrammeStart = false;
  if (data.hasOwnProperty('start')) {
    hasProgrammeStart = true;

    if (typeof data.start !== 'string' || !moment(data.start, DATE_FORMAT, true).isValid()) {
      throw new InvalidUpdateError(
        'Provided programme update values did not have start attribute (or it was not valid)'
      );
    }
  }

  // Check the financialStart attribute if it exists
  let hasFinancialStart = false;
  if (data.hasOwnProperty('financialStart')) {
    hasFinancialStart = true;

    if (
      typeof data.financialStart !== 'string' ||
      !moment(data.financialStart, DATE_FORMAT, true).isValid()
    ) {
      throw new InvalidUpdateError(
        'Provided programme update values did not have financialStart attribute (or it not valid)'
      );
    }
  }

  // Check the scorecardStart attribute if it exists
  let hasScorecardStart = false;
  if (data.hasOwnProperty('scorecardStart')) {
    hasScorecardStart = true;

    if (
      typeof data.scorecardStart !== 'string' ||
      !moment(data.scorecardStart, DATE_FORMAT, true).isValid()
    ) {
      throw new InvalidUpdateError(
        'Provided programme update values did not have scorecardStart attribute (or it was not valid)'
      );
    }
  }

  // Get programme start for comparison purposes
  const comparisonDate = hasProgrammeStart
    ? moment(data.start, DATE_FORMAT, true)
    : getProgrammeStart(model);

  const financialStartComparison = hasFinancialStart
    ? moment(data.financialStart, DATE_FORMAT, true)
    : getProgrammeFinancialStart(model);

  const scorecardStartComparison = hasScorecardStart
    ? moment(data.scorecardStart, DATE_FORMAT, true)
    : getProgrammeScorecardStart(model);

  if (hasProgrammeStart) {
    if (financialStartComparison.isAfter(moment(data.start, DATE_FORMAT, true))) {
      throw new InvalidUpdateError('Programme start occured before financial start');
    } else if (scorecardStartComparison.isAfter(moment(data.start, DATE_FORMAT, true))) {
      throw new InvalidUpdateError('Programme start occured before scorecard start');
    }
  }

  // Check whether financial start is consistent with other start dates
  if (hasFinancialStart) {
    if (moment(data.financialStart, DATE_FORMAT, true).isAfter(comparisonDate)) {
      throw new InvalidUpdateError('Financial start occured after programme start');
    }
  }

  // Check whether scorecard start is consistent with other start dates
  if (hasScorecardStart) {
    if (moment(data.scorecardStart, DATE_FORMAT, true).isAfter(comparisonDate)) {
      throw new InvalidUpdateError('Scorecard start occured after programme start');
    }
  }

  return true;
}

/**
 * Updates a programme instance with the provided values
 * @param {Model} model model instance
 * @param {Programme} values programme values
 */
export function updateProgramme(model: Model, values: ProgrammeData): Model {
  checkProgrammeData(values, model);

  const programme = getProgramme(model);

  let newBenefits;
  const benefits = getBenefits(model);
  if (values.hasOwnProperty('unitOfMeasure')) {
    // Update financial benefit units of measure
    newBenefits = benefits.map(benefit => {
      if (
        benefit.type === BENEFIT_TYPE_FINANCIAL &&
        benefit.id !== model.programmes[0]._coreBenefits[2]
      ) {
        return {
          ...benefit,
          unitOfMeasure: values.unitOfMeasure,
        };
      } else {
        return benefit;
      }
    });
  } else {
    newBenefits = benefits;
  }

  return {
    ...model,
    programmes: [
      {
        ...programme,
        benefits: newBenefits,
        name: values.hasOwnProperty('name') ? values.name : programme.name,
        mission: values.hasOwnProperty('mission') ? values.mission : programme.mission,
        vision: values.hasOwnProperty('vision') ? values.vision : programme.vision,
        unitOfMeasure: values.hasOwnProperty('unitOfMeasure')
          ? values.unitOfMeasure
          : programme.unitOfMeasure,
        start: values.hasOwnProperty('start') ? values.start : programme.start,
        financialStart: values.hasOwnProperty('financialStart')
          ? values.financialStart
          : programme.financialStart,
        scorecardStart: values.hasOwnProperty('scorecardStart')
          ? values.scorecardStart
          : programme.scorecardStart,
      },
    ],
  };
}

/**
 * Returns the programme id
 * @param  {Model} model model instance
 * @return {string}      id
 **/
export function getProgrammeId(model: Model): string {
  const programme = getProgramme(model);
  if (!programme.id) {
    throw new InvalidModelError("Programme did not contain the 'id' attribute");
  } else if (typeof programme.id !== 'string') {
    throw new InvalidModelError("The programme's 'id' attribute was not a string");
  }

  return programme.id;
}

