/**
 * Returns the programme start
 * @param  {Model} model model instance
 * @return {string}      start
 **/
export function getProgrammeStart(model: Model): moment {
  const programme = getProgramme(model);
  if (!programme.start) {
    throw new InvalidModelError("Programme did not contain the 'start' attribute");
  } else if (typeof programme.start !== 'string') {
    throw new InvalidModelError("The programme's 'start' attribute was not a string");
  }

  // Try to parse as moment
  const date = moment(programme.start, DATE_FORMAT, true);

  // If failed throw location-specific error
  if (!date.isValid()) {
    throw new InvalidModelError(
      `The programme's 'start' attribute was not in valid date format ${DATE_FORMAT}`
    );
  }

  return date;
}

