/**
 * Returns the programme name
 * @param  {Model} model model instance
 * @param  {string} name new name
 * @return {Model}      updated model instance
 **/
export function setProgrammeName(model: Model, name: string): Model {
  if (!name) {
    throw new InvalidUpdateError('Name must be defined to update programme attribute');
  } else if (typeof name !== 'string') {
    throw new InvalidUpdateError(
      `Updating the programme name to ${name} is invalid, name must be a string`
    );
  }

  // Get the programme instance
  const programme = getProgramme(model);

  // Return new model instance with updated programme values
  return {
    ...model,
    programmes: [
      {
        ...programme,
        name,
      },
    ],
  };
}

