/**
 * Returns the programme unit of measure
 * @param  {Model} model model instance
 * @param  {string} unitofmeasure new unit of measure
 * @return {string}      Modelof updated model instance
 **/
export function setProgrammeUnitOfMeasure(model: Model, unitOfMeasure: string): Model {
  if (!unitOfMeasure) {
    throw new InvalidUpdateError(
      'Updating the Unit of measure must be defined to update programme attribute'
    );
  } else if (typeof unitOfMeasure !== 'string') {
    throw new InvalidUpdateError(
      `Updating the programme unit of measure to ${unitOfMeasure} is invalid, unit of measure must be a string`
    );
  }

  // Get the programme instance
  const programme = getProgramme(model);

  // Update financial benefit units of measure
  const benefits = getBenefits(model);
  const newBenefits = benefits.map(benefit => {
    if (
      benefit.type === BENEFIT_TYPE_FINANCIAL &&
      benefit.id !== model.programmes[0]._coreBenefits[2]
    ) {
      return {
        ...benefit,
        unitOfMeasure,
      };
    } else {
      return benefit;
    }
  });

  // Return new model instance with updated programme values
  return {
    ...model,
    programmes: [
      {
        ...programme,
        unitOfMeasure,
        benefits: newBenefits,
      },
    ],
  };
}

/**
 * Returns the programme start
 * @param  {Model} model model instance
 * @return {string}      start
 **/
export function setProgrammeStart(model: Model, start: string): Model {
  if (!start) {
    throw new InvalidUpdateError('Cannot update programme start as it was not defined');
  } else if (typeof start !== 'string') {
    throw new InvalidUpdateError('New programme start was not a string');
  }

  // Try to parse as moment
  const m = moment(start, DATE_FORMAT, true);
  if (!m.isValid()) {
    throw new InvalidUpdateError(`Provided start was not in correct date format ${DATE_FORMAT}`);
  }

  if (m.isBefore(getProgrammeFinancialStart(model))) {
    throw new InvalidUpdateError('Provided start occured before the financial start');
  } else if (m.isBefore(getProgrammeScorecardStart(model))) {
    throw new InvalidUpdateError('Provided start occured before the scorecard start');
  }

  // Get Programme instance
  const programme = getProgramme(model);

  return {
    ...model,
    programmes: [
      {
        ...programme,
        start,
      },
    ],
  };
}

/**
 * Returns the programme financial start
 * @param  {Model} model model instance
 * @return {string}      financial start
 **/
export function setProgrammeFinancialStart(model: Model, financialStart: string): Model {
  if (!financialStart) {
    throw new InvalidUpdateError('Cannot update programme financial start as it was not defined');
  } else if (typeof financialStart !== 'string') {
    throw new InvalidUpdateError('New programme financial start was not a string');
  }

  // Try to parse as moment
  const m = moment(financialStart, DATE_FORMAT, true);
  if (!m.isValid()) {
    throw new InvalidUpdateError(
      `Provided financial start was not in correct date format ${DATE_FORMAT}`
    );
  }

  if (m.isAfter(getProgrammeStart(model))) {
    throw new InvalidUpdateError('Provided financial start occured after the programme start');
  }

  // Get Programme instance
  const programme = getProgramme(model);

  return {
    ...model,
    programmes: [
      {
        ...programme,
        financialStart,
      },
    ],
  };
}

